.portofolio-page {
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        article {
            .portofolio-txt {
                header {
                    color: #000000;
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                    }
                    h5 {
                        @media(max-width: 600px){
                            font-size: 17px;
                        }
                        font-family: "Poppins-Regular";
                        font-size: 25px;
                    }
                }
                article {
                    p {
                        @media(max-width: 600px){
                            font-size: 15px;
                        }
                        color: #909295;
                        font-size: 18px;
                        margin: 35px 0;
                        font-family: "Poppins-Light";
                    }
                    time {
                        @media(max-width: 600px){
                            font-size: 15px;
                        }
                        color: #909295;
                        font-size: 18px;
                        font-family: "Poppins-Light";
                        margin-bottom: 35px;
                        display: block;
                    }
                    ul {
                        display: flex;
                        flex-wrap: wrap;
                        li {
                            @media(max-width: 600px){
                                font-size: 10px;
                            }
                            color: #909295;
                            border: 2px solid #CED0D1;
                            border-radius: 5px;
                            font-family: "Poppins-Regular";
                            margin-right: 5px;
                            margin-bottom: 5px;
                            padding: 5px 10px;
                            font-size: 14px;
                        }
                    }
                }
            }
        }
    }
}