.services-page {
    background-color: #000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 4rem 0;
        article {
            //** Start Services Needed **//
            .services-needed {
                header {
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                        margin-bottom: 5px;
                    }
                }
                article {
                    .tabs-section {
                        .search {
                            input {
                                background: #E4E5E6;
                                border-radius: 32px;
                                border: none;
                                height: 40px;
                                padding: 0 18px;
                                font-size: 18px;
                                outline: none;
                                font-family: "Poppins-Medium";
                                &:focus {
                                    box-shadow: none;
                                }
                            }
                            button {
                                background-color: $mainColor;
                                border-radius: 32px;
                                min-width: 45px;
                                height: 40px;
                                border: none;
                                outline: none;
                                transition: all .3s;
                                &:hover {
                                    transform: scale(1.05) translateY(-5px);
                                }
                                img {
                                    width: 20px;
                                }
                            }
                        }
                    }
                    ul.nav {
                        // margin: 1rem 0 3rem 0 !important;
                        li {
                            button {
                                background: #E4E5E6;
                                padding: 5px;
                                min-width: 165px;
                                border: none;
                                margin-right: 10px;
                                min-height: 40px;
                                border-radius: 25px;
                                font-family: "Poppins-Medium";
                                transition: all .3s;
                                &.active {
                                    background-color: $mainColor;
                                    box-shadow: none;
                                    color: #000;
                                }
                                &:hover {
                                    background-color: $mainColor;
                                    box-shadow: none;
                                    color: #000;
                                }
                            }
                        }
                    }
                    .logo-content {
                        .logo-types {
                            .types-card {
                                box-shadow: 0px 0px 35px #6868684f;
                                padding: 25px;
                                border-radius: 25px;
                                &:hover {
                                    .media {
                                        img {
                                            filter: unset;
                                        }
                                    }
                                    .card-footer {
                                        button {
                                            opacity: 1;
                                        }
                                    }
                                }
                                .most-popular {
                                    p {
                                        background-color: #000;
                                        color: $mainColor;
                                        display: inline-block;
                                        padding: 6px;
                                        font-size: 14px;
                                        border-radius: 50px;
                                        min-width: 140px;
                                        text-align: center;
                                        font-family: "Poppins-Medium";
                                    }
                                }
                                .media {
                                    margin-top: 15px;
                                    img {
                                        border-radius: 15px;
                                        width: 60px;
                                        height: 60px;
                                        filter: grayscale(100%);
                                        transition: all .3s;
                                    }
                                    h5 {
                                        font-family: "Poppins-SemiBold";
                                        color: #000000;
                                        font-size: 17px;
                                        margin-bottom: 5px;
                                    }
                                    p {
                                        color: #6D6E70;
                                        font-family: "Poppins-Regular";
                                        font-size: 15px;
                                        span {
                                            &:last-child {
                                                color: #37B14A;
                                                border: 2px solid #37B14A;
                                                border-radius: 4px;
                                                margin-left: 15px;
                                                padding: 2px 9px;
                                            }
                                        }
                                    }
                                }
                                .content {
                                    p {
                                        color: #A4A6A9;
                                        font-family: "Poppins-Regular";
                                        font-size: 15px;
                                        margin: 10px 0;
                                    }
                                    li {
                                        color: #A4A6A9;
                                        font-family: "Poppins-Regular";
                                        line-height: 30px;
                                    }
                                }
                                .card-footer {
                                    text-align: end;
                                    background-color: transparent;
                                    border: none;
                                    padding: 0;
                                    button {
                                        transition: all .3s;
                                        opacity: 0;
                                        font-family: "Poppins-SemiBold";
                                        font-size: 15px;
                                        color: #000;
                                        background-color: $mainColor;
                                        border: none;
                                        outline: none;
                                        border-radius: 25px;
                                        padding: 5px;
                                        min-width: 120px;
                                        transition: all .3s;
                                        &:hover {
                                            background-color: #000;
                                            color: $mainColor;
                                        }
                                    }
                                }
                            }
                        }
                        .price-card {
                            border: 1px solid #D8D8D8;
                            border-radius: 15px;
                            padding: 5rem 30px;
                            transition: all .3s;
                            position: relative;
                            &:hover {
                                box-shadow: 0px 0px 35px #00000029;
                                transform: scale(1.01);
                                .action {
                                    button {
                                        opacity: 1;
                                    }
                                }
                            }
                            h3 {
                                font-family: "Poppins-Bold";
                                color: #000000;
                                text-align: center;
                                span {
                                    font-size: 30px;
                                }
                                strong {
                                    font-size: 60px;
                                    margin: 0 5px;
                                }
                                sup {
                                    font-size: 32px;
                                }
                            }
                            h5 {
                                font-family: "Poppins-SemiBold";
                                font-size: 23px;
                                margin: 35px 0 24px;
                                text-align: center;
                                color: #000;
                            }
                            ul {
                                li {
                                    line-height: 30px;
                                    span {
                                        color: #717171;
                                        font-family: "Poppins-Regular";
                                        font-size: 18px;
                                    }
                                }
                            }
                            .action {
                                text-align: center;
                                margin-top: 2rem;
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                display: flex;
                                align-items: center;
                                margin-bottom: 25px;
                                justify-content: center;
                                width: 100%;
                                button {
                                    margin-top: 15px;
                                    transition: all .3s;
                                    opacity: 0;
                                    font-family: "Poppins-SemiBold";
                                    font-size: 15px;
                                    color: #000;
                                    background-color: $mainColor;
                                    border: none;
                                    outline: none;
                                    border-radius: 25px;
                                    padding: 5px;
                                    min-width: 120px;
                                    transition: all .3s;
                                    &:hover {
                                        background-color: #000;
                                        color: $mainColor;
                                    }
                                }
                            }
                        }
                        .logo-slider {
                            margin: 3rem 0;
                            .slider {
                                border-radius: 15px;
                                overflow: hidden;
                                .carousel.slide {
                                    .carousel-indicators {
                                        align-items: center;
                                        li {
                                            width: 8px;
                                            height: 8px;
                                            border-radius: 50%;
                                            background-color: #fff;
                                            opacity: 1;
                                            transition: all .3s;
                                            &.active {
                                                border-radius: 10px;
                                                width: 50px;
                                                height: 5px;
                                                background-color: $mainColor;
                                                border-radius: 2px;
                                                border: aliceblue;
                                            }
                                        }
                                    } 
                                    .carousel-inner {
                                        .carousel-item {
                                            background: url('../assets/home/home.jpg');
                                            background-repeat: no-repeat;
                                            background-size: cover;
                                            background-position: inherit;
                                            .content {
                                                min-height: 300px;
                                                .carousel-caption {
                                                    position: static;
                                                    h1 {
                                                        @media(max-width: 600px) {
                                                            font-size: 20px;
                                                        }
                                                        text-transform: capitalize;
                                                        font-family: "Poppins-Bold";
                                                        font-size: 35px;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        .logo-cards {
                            .col-lg-3.col-md-4.col-sm-6 {
                                border: 1px solid #D8D8D8;
                                border-radius: 15px;
                                background-color: #fff;
                            }
                            .logo-card {
                                padding: 25px;
                                transition: all .2s;
                                position: relative;
                                z-index: 99;
                                &:hover {
                                    box-shadow: 0 7px 21px 0 #0000001a;
                                    transform: scale(1.05,1.05);
                                    border-radius: 15px;
                                    background: #fff;
                                    img {
                                        filter: unset;
                                    }
                                    .footer {
                                        button {
                                            opacity: 1;
                                        }
                                    }
                                }
                                img {
                                    border-radius: 15px;
                                    width: 60px;
                                    height: 60px;
                                    filter: grayscale(100%);
                                    transition: all .3s;
                                }
                                .content {
                                    h5 {
                                        margin-top: 15px;
                                        font-family: "Poppins-SemiBold";
                                        color: #000000;
                                        font-size: 17px;
                                        margin-bottom: 5px;
                                    }
                                    span {
                                        font-size: 15px;
                                        color: #6D6E70;
                                        font-family: "Poppins-Regular";
                                    }
                                    p {
                                        color: #A4A6A9;
                                        font-family: "Poppins-Regular";
                                        font-size: 15px;
                                        margin: 5px 0;
                                    }
                                }
                                .footer {
                                    button {
                                        margin-top: 15px;
                                        transition: all .3s;
                                        opacity: 0;
                                        font-family: "Poppins-SemiBold";
                                        font-size: 15px;
                                        color: #000;
                                        background-color: $mainColor;
                                        border: none;
                                        outline: none;
                                        border-radius: 25px;
                                        padding: 5px;
                                        min-width: 120px;
                                        transition: all .3s;
                                        &:hover {
                                            background-color: #000;
                                            color: $mainColor;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            //** End Services Needed **//
            }
        }
    }
}