.error-page {
    .error-content {
        background: $mainColor;
        min-height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        .data-content{
            h4 {
                font-weight: bold;
                color: #000000;
                font-size: 28px;
            }
            img {
                &:last-of-type {
                    max-width: 300px;
                }
            }
            button{
                color: #000000;
                background-color: transparent;
                border: 4px solid #000000;
                outline: none;
                padding: 5px;
                font-size: 16px;
                font-family: "Poppins-Bold";
                border-radius: 25px;
                min-width: 240px;
                transition: all .3s;
                &:hover {
                    background-color: #000000;
                    color: $mainColor;
                }
            }
        }
    }
}