.privacy-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 7rem 0;
        article {
            h4 {
                color: #000000;
                font-family: "Poppins-Bold";
                font-size: 25px;
            }
            a {
                color: #25A7DE;
                font-family: "Poppins-Bold";
                font-size: 20px;
            }
            p {
                color: #000000;
                font-family: "Poppins-Regular";
                font-size: 18px;
            }
        }
    }
}