.blog-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 4rem 0;
        article {
            //** Start Topics Search **//
            .topic-search {
                header {
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                        margin-bottom: 5px;
                    }
                }
                article {
                    p {
                        color: #404041;
                        font-size: 18px;
                        font-family: "Poppins-Regular";
                    }
                    form {
                        margin-top: 30px;
                        font-family: "Poppins-Medium";
                        input {
                            background: #E4E5E6;
                            border-radius: 32px;
                            border: none;
                            padding-left: 18px;
                            padding-right: 18px;
                            &:focus {
                                box-shadow: none;
                            }
                        }
                        button {
                            background-color: $mainColor;
                            border-radius: 32px;
                            min-width: 45px;
                            border: none;
                            outline: none;
                            transition: all .3s;
                            &:hover {
                                transform: scale(1.05) translateY(-5px);
                            }
                            img {
                                width: 20px;
                            }
                        }
                        select {
                            border: none;
                            color: #000000;
                            &:focus {
                                box-shadow: none;
                            }
                        }
                    }
                }
            }
            //** End Topics Search **//

            //** Start Topics Card **//
            .topics-cards {
                a {
                    text-decoration: none;
                }
                .topic-card {
                    background-color: #000000;
                    border-radius: 11px;
                    overflow: hidden;
                    &:hover {
                        .card-img {
                            img {
                                transform: scale(1.05);
                            }
                        }
                    }
                    .card-img {
                        overflow: hidden;
                        img {
                            transition: all .4s ease-in-out;
                        }
                    }
                    .card-body {
                        color: #fff;
                        padding: 10px 20px;
                        time {
                            font-family: "Poppins-Bold";
                            font-size: 14px;
                        }
                        h4 {
                            font-family: "Poppins-Bold";
                            font-size: 18px;
                            margin: 7px 0;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                        }
                        p {
                            font-size: 15px;
                            font-family: "Poppins-Regular";
                            margin: 0;
                        }
                    }
                }
            }
            //** End Topics Card **//
        }
    }

    .pagination {
        justify-content: center;
        margin-top: 2em;
        flex-wrap: wrap;
        li {
            margin: 5px;
            &.previous {
                a {
                    border-radius: 24px;
                    background: #E4E5E6;
                    color: #6D6E70;
                    font-weight: 500;
                    min-width: 122px;
                    text-align: center;
                    transition: all .3s;
                    &.active,
                    &:hover {
                        background: #CED0D1;
                        color: #6D6E70;
                        border-radius: 47px;
                        border-color: #CED0D1;
                    }
                }
            }
            &.next {
                a {
                    border-radius: 24px;
                    background: $mainColor;
                    color: #000000;
                    font-weight: 500;
                    min-width: 122px;
                    text-align: center;
                    transition: all .3s;
                    &.active,
                    &:hover {
                        background: #000;
                        color: $mainColor;
                        border-radius: 47px;
                        border-color: #000;
                    }
                }
            }
            a {
                border-radius: 47px;
                border: 1px solid #E4E5E6;
                color: #8f8f8f;
                font-family: "Poppins-Regular";
                &.active,
                &:hover {
                    background-color: $mainColor;
                    color: #000000;
                    border-radius: 47px;
                    border: 1px solid $mainColor;
                }
                &:focus {
                    box-shadow: none;
                }
            }
        }
    }
}