.place-your-order-form-page {
    background-color: #000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        article {
            input[type=checkbox] {
                position: absolute;
                opacity: 0;
            }
            /* Styling Parts */
            #part1,
            #part2,
            #part3,
            #part4,
            #part5,
            #part6,
            #part7 {
                z-index: 2;
                display: block;
                height: auto;
                opacity: 1;
                transition: opacity 1s ease-in-out;
            }
              /* Hidden Parts */
            #part2,
            #part3,
            #part4,
            #part5,
            #part6,
            #part7 {
                opacity: 0;
                height: 0;
                overflow: hidden;
            }
            /* Show Step 2 & Hide Step 1 */
            #step2:checked ~ #part2 {
                opacity: 1;
                height: auto;
            }
            #step2:checked ~ #part1 {
                opacity: 0;
                height: 0;
                display: none;
            }
            /* Show Step 3  & Hide Step 2 */
            #step3:checked ~ #part3 {
                opacity: 1;
                height: auto;
            }
            #step3:checked ~ #part2 {
                opacity: 0;
                width: 0;
                height: 0;
            }
            /* Show Step 4  & Hide Step 3 */
            #step4:checked ~ #part4 {
                opacity: 1;
                height: auto;
            }
            #step4:checked ~ #part3 {
                opacity: 0;
                width: 0;
                height: 0;
            }
            /* Show Step 5  & Hide Step 4 */
            #step5:checked ~ #part5 {
                opacity: 1;
                height: auto;
            }
            #step5:checked ~ #part4 {
                opacity: 0;
                width: 0;
                height: 0;
            }
            /* Show Step 6  & Hide Step 5 */
            #step6:checked ~ #part6 {
                opacity: 1;
                height: auto;
            }
            #step6:checked ~ #part5 {
                opacity: 0;
                width: 0;
                height: 0;
            }
            /* Show Step 7  & Hide Step 6 */
            #step7:checked ~ #part7 {
                opacity: 1;
                height: auto;
            }
            #step7:checked ~ #part6 {
                opacity: 0;
                width: 0;
                height: 0;
            }

            //** Start Step Indicator **//
            .step-parent {
                padding: 2rem 0;
                box-shadow: 0px 12px 13px #E5E5E5BF;
                .step-indicator {
                    display: flex;
                    align-items: center;
                    max-width: 420px;
                    margin: auto;
                    height: 30px;
                    .step {
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        position: relative;
                        z-index: 1;
                        .step-icon {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            height: 30px;
                            width: 30px;
                            border-radius: 50%;
                            background: #c2c2c259;
                            font-size: 10px;
                            text-align: center;
                            color: #000;
                            position: relative;
                            font-size: 16px;
                            font-family: "Poppins-Bold";
                        }
                        &.active .step-icon {
                            background: $mainColor;
                        }
                        &.finished {
                            .step-icon {
                                background-color: #fff;
                                border: 3px solid $mainColor;
                                img {
                                    width: 26px;
                                    position: relative;
                                    top: -3px;
                                    left: 7px;
                                }
                            }
                        }
                    }
                    .indicator-line {
                        width: 100%;
                        height: 3px;
                        background: #c2c2c259;
                        flex: 1;
                        &.active {
                            background: $mainColor;
                        }
                    }
                }
                .step-indicator-para {
                    h4 {
                        @media(max-width: 600px) {
                            font-size: 18px;
                        }
                        text-align: center;
                        color: #000000;
                        font-family: "Poppins-Bold";
                        font-size: 22px;
                        margin-top: 20px;
                        margin-bottom: 0;
                    }
                }
            }
            //** End Step Indicator **//

            //** Start Step1 Form **//
            .step1 {
                padding: 4rem 0;
                .services-type {
                    .service {
                        overflow: hidden;
                        label {
                            @media(max-width: 600px) {
                                font-size: 18px;
                            }
                            border: 3px solid #FFDD00;
                            border-radius: 14px;
                            cursor: pointer;
                            width: 100%;
                            height: 100%;
                            color: #000;
                            font-size: 22px;
                            font-family: "Poppins-Bold";
                            min-height: 145px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                        input[type="radio"] {
                            display: none;
                            z-index: 100;
                        }
                        input[type="radio"]:checked + label {
                            background: $mainColor;
                            border-radius: 14px;
                        }
                    }
                }
                .services-info {
                    padding: 3rem 0;
                    header {
                        h3 {
                            @media(max-width: 600px) {
                                font-size: 20px;
                            }
                            color: #000000;
                            font-size: 35px;
                            font-family: "Poppins-Bold";
                            margin-bottom: 1.5rem;
                        }
                    }
                    article {
                        p {
                            @media(max-width: 600px) {
                                font-size: 14px;
                            }
                            color: #6D6E70;
                            font-size: 18px;
                            font-family: "Poppins-Light";
                        }
                    }
                }
            }
             //** End Step1 Form **//
            
             //** End Step2 Form **//
            .step2 {
                padding: 4rem 0;
                .services-type {
                    .service {
                        overflow: hidden;
                        label {
                            @media(max-width: 600px) {
                                font-size: 18px;
                            }
                            border: 3px solid #FFDD00;
                            border-radius: 14px;
                            cursor: pointer;
                            width: 100%;
                            height: 100%;
                            color: #000;
                            font-size: 22px;
                            font-family: "Poppins-Bold";
                            min-height: 145px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                        input[type="radio"] {
                            display: none;
                            z-index: 100;
                        }
                        input[type="radio"]:checked + label {
                            background: $mainColor;
                            border-radius: 14px;
                        }
                    }
                }
                .section-counter {
                    .number {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        margin-bottom: 20px;
                        label {
                            @media(max-width: 600px) {
                                font-size: 17px;
                            }
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 20px;
                            margin: 0;
                        }
                        button {
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 25px;
                            background-color: $mainColor;
                            border: none;
                            outline: none;
                            width: 35px;
                            &.minus {
                                border-radius: 8px 0 0 8px;
                            }
                            &.plus {
                                border-radius: 0 8px 8px 0;
                            }
                        }
                        input {
                            width: 40px;
                            border: none;
                            outline: none;
                            text-align: center;
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 25px;
                        }
                    }
                    .categories {
                        select {
                            @media(max-width: 600px) {
                                font-size: 17px;
                            }
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 20px;
                            border: none;
                            padding: 0;
                            &:focus {
                                box-shadow: none;
                            }
                        }
                    }
                }
            }
             //** End Step2 Form **//
            
             //** Start Step3 Form **//
            .step3 {
                padding: 4rem 0;
                .questions-sec {
                    margin-bottom: 3rem;
                    label {
                        color: #000000;
                        font-size: 18px;
                        font-family: "Poppins-Medium";
                    }
                    textarea {
                        border: 3px solid #FFDD00;
                        border-radius: 14px;
                        &:focus {
                            box-shadow: none;
                        }
                    }
                }
            }
             //** End Step3 Form **//
            
             //** Start Step4 & Step5 Form **//
            .step4, .step5 {
                padding: 4rem 0;
                .services-type {
                    margin-bottom: 3rem;
                    .service {
                        overflow: hidden;
                        label {
                            @media(max-width: 600px) {
                                font-size: 18px;
                            }
                            position: relative;
                            border: 3px solid #BFBFBF;
                            border-radius: 14px;
                            cursor: pointer;
                            width: 100%;
                            height: 100%;
                            color: #000;
                            font-size: 22px;
                            font-family: "Poppins-Bold";
                            min-height: 145px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            .checked-icon {
                                position: absolute;
                                top: 0px;
                                right: 7px;
                                img {
                                    opacity: 0;
                                    transition: all .3s;
                                    width: 20px;
                                    height: 20px;
                                }
                            }
                        }
                        input[type="radio"] {
                            display: none;
                            z-index: 100;
                        }
                        input[type="radio"]:checked + label {
                            border-color: $mainColor;
                            border-radius: 14px;
                            .checked-icon {
                                img {
                                    opacity: 1;
                                }
                            }
                        }
                    }
                }
            }
             //** End Step4 & Step5 Form **//

             //** Start Step6 Form **//
            .step6 {
                padding: 4rem 0;
                .questions-sec {
                    margin-bottom: 3rem;
                    label {
                        color: #000000;
                        font-size: 18px;
                        font-family: "Poppins-Medium";
                    }
                    textarea {
                        border: 3px solid #FFDD00;
                        border-radius: 14px;
                        &:focus {
                            box-shadow: none;
                        }
                    }
                }
                .upload-file {
                    margin-bottom: 3rem;
                    text-align: center;
                    p {
                        color: #000000;
                        font-size: 18px;
                        font-family: "Poppins-Medium";
                    }
                    .file-input {
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        width: 100%;
                        font-family: sans-serif;
                        display: inline-block;
                        text-align: left;
                        padding: 8px 0;
                        position: relative;
                        width: 100%;
                    }
                    .file-input > [type="file"] {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                        z-index: 10;
                        cursor: pointer;
                    }
                    .file-input > .button {
                        display: inline-block;
                        cursor: pointer;
                        padding: 1px 16px;
                        border-radius: 25px;
                        margin-right: 8px;
                        font-size: 20px;
                        font-weight: bold;
                        color: #000000;
                        background-color: transparent;
                        border: 3px solid $mainColor;
                        outline: none;
                        font-family: BebasNeueBold;
                    }
                    .file-input > .label {
                        color: #6D6E70;
                        font-family: "Poppins-Light";
                    }
                    #preview {
                        max-width: 180px;
                        max-height: 160px;
                        margin-top: 16px;
                        object-fit: cover;
                    }
                    span.button{
                        img {
                            width: 18px;
                            height: 18px;
                            margin-right: 5px;
                        }
                    }
                }
            }
             //** End Step65 Form **//
            
            .section-action {
                text-align: end;
                .back {
                    .btn {
                        img {
                            width: 20px;
                            transition: all .3s ease;
                        }
                        margin: 5px;
                        cursor: pointer;
                        background: transparent;
                        color: #CED0D1;
                        border: 2px solid #CED0D1;
                        padding: 5px 5px;
                        min-width: 145px;
                        border-radius: 25px;
                        outline: none;
                        font-family: "Poppins-Bold";
                        &:hover {
                            img {
                                transform: translateX(-5px);
                            }
                        }
                    }
                }
                .continue {
                    .btn {
                        img {
                            width: 20px;
                            transition: all .3s ease;
                        }
                        cursor: pointer;
                        background: $mainColor;
                        border: none;
                        border: 2px solid $mainColor;
                        padding: 5px 5px;
                        min-width: 145px;
                        border-radius: 25px;
                        outline: none;
                        font-family: "Poppins-Bold";
                        &:hover {
                            img {
                                transform: translateX(5px);
                            }
                        }
                    }
                }
                .skip {
                    .btn {
                        font-family: "Poppins-Bold";
                        background-color: #EBEBEB;
                        color: #B5B5B5;
                        cursor: pointer;
                        border: none;
                        border: 2px solid #EBEBEB;
                        padding: 5px 5px;
                        min-width: 100px;
                        border-radius: 25px;
                        outline: none;
                        transition: all .3s;
                        &:hover {
                            background-color: #CED0D1;
                            color: #6D6E70;
                        }
                    }
                }
                .add-btn {
                    button {
                        img {
                            width: 20px;
                            transition: all .3s ease;
                        }
                        cursor: pointer;
                        background: $mainColor;
                        border: none;
                        border: 2px solid $mainColor;
                        padding: 5px 5px;
                        min-width: 145px;
                        border-radius: 25px;
                        outline: none;
                        font-family: "Poppins-Bold";
                        &:hover {
                            img {
                                transform: translateX(5px);
                            }
                        }
                    }
                }
                .submit-btn {
                    button {
                        cursor: pointer;
                        background: #37B14A;
                        border: none;
                        border: 2px solid #37B14A;
                        color: #fff;
                        padding: 5px 5px;
                        min-width: 145px;
                        border-radius: 25px;
                        outline: none;
                        font-family: "Poppins-Bold";
                        transition: all .3s;
                        &:hover {
                            border-color: #258734;
                            background-color: #258734;
                        }
                    }
                }
            }
        }
    }
}

