.impressum-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        article {
            //** Start Who We Are & Vision & Mission & Part of US Section **//
            .impressum-txt {
                padding: 3rem 0;
                .content {
                    position: relative;
                    padding-left: 45px;
                    &::after {
                        content: '';
                        background: url("../assets/icons/text-icon.png");
                        background-size: contain;
                        background-repeat: no-repeat;
                        height: 33px;
                        width: 100px;
                        position: absolute;
                        left: 0px;
                        top: 7px;
                    }
                    header {
                        h3 {
                            color: #000000;
                            @media(max-width: 600px){
                                font-size: 22px;
                            }
                            font-family: "Poppins-Bold";
                            font-size: 35px;
                            margin-bottom: 25px;
                        }
                    }
                    article {
                        p {
                            @media(max-width: 600px){
                                font-size: 15px;
                            }
                            color: #000000;
                            font-family: "Poppins-Regular";
                            font-size: 18px;
                            margin: 0;
                            span {
                                display: block;
                            }
                        }
                    }
                }
            }
            //** End Who We Are & Vision & Mission & Part of US Section **//
        }
    }
}