.navbar-section {
    background-color: #000000;
    .navbar-brand {
        img {
            width: 115px;
        }
    }
    .navbar-toggler {
        outline: none;
        .toggle-menu {
            display: block;
            position: relative;
            text-indent: -999em;
            cursor: pointer;
            width: 35px;
            height: 35px;
        }
        .toggle-menu--clicked {
            .menu__bar {
                
                &:nth-child(1) {
                    top: 1.25rem;
                    transform: rotate(45deg);
                }
                
                &:nth-child(2) {
                    transform: translateX(100%) scale(0);
                }
                
                &:nth-child(3) {
                    top: 1.25rem;
                    transform: rotate(-45deg);
                }
            }
        }
        .menu__bar {
            display: block;
            position: absolute;
            left: 0;
            width: 30px;
            height: 5px;
            transition: top .4s ease-out, transform .4s ease-out;
            background-color: $mainColor;
            
            &:nth-child(1) {
                top: 5px;
            }
            
            &:nth-child(2) {
                top: 15px;
            }
            
            &:nth-child(3) {
                top: 25px;
            }
        }
    }
    .navbar-nav {
        @media(min-width: 992px) {
            width: 50%;
            justify-content: space-around;
            // align-items: center;
            position: relative;
            top: 5px;
        }
        @media(max-width: 992px) {
            padding-top: 20px;
        }
        .nav-item {
            .nav-link {
                @media (min-width: 1160px) {
                    margin-right: 0.9rem;
                    margin-left: 0.9rem;
                    padding-left: 0;
                    padding-right: 0;
                }
                color: #FFFFFF;
                font-family: "Poppins-SemiBold";
                font-size: 13px;
                white-space: nowrap;
                position: relative;
                overflow: hidden;
                display: inline-block;
                &.dropdown-toggle {
                    overflow: visible;
                    &:after {
                        all: unset;
                    }
                }
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    height: 3px;
                    width: 0;
                    background: transparent;
                }
                &:after {
                    transition: width .5s ease, background-color .5s ease, left .5s ease;
                }
                &:hover:after {
                    width: 100%;
                    left: 0;
                    background: #fff;
                }
            }
        }
    }
    .form-inline {
        .navbar-nav {
            width: auto;
            @media(max-width: 992px) {
                padding-top: 0px;
            }
            .nav-item {
                @media(min-width: 992px) {
                    &:first-child {
                        margin-right: 20px;
                    }
                    &:last-child {
                        margin-left: 20px;
                    }
                }
                .nav-link {
                    color: $mainColor;
                    font-family: "ProximaNovaExtrabold";
                    &:after {
                        background: $mainColor;
                    }
                    &:hover {
                        &:before {
                            background: $mainColor;
                        }
                    }
                    &.dropdown-toggle {
                        &:after {
                            background: transparent;
                        }
                        &:hover {
                            &:before {
                                background: transparent;
                            }
                        }
                    }
                }
                .dropdown-toggle::after{
                    font-family: "Font Awesome 5 Free";
                    content: "\f078";
                    display: inline-block;
                    padding-right: 3px;
                    vertical-align: middle;
                    font-weight: 900;
                    border: none;
                }
                .dropdown-menu {
                    background-color: #000000;
                    padding: 0;
                    // overflow: hidden;
                    border: 2px solid #FFDD00;
                    padding: 10px;
                    &::before {
                        @media(max-width: 992px) {
                            content: '';
                        }
                        font-family: "Font Awesome 5 Free";
                        content: "\f077";
                        display: inline-block;
                        font-weight: 900;
                        color: #FFDD00;
                        position: absolute;
                        top: -16px;
                        left: 19px;
                        background: black;
                    }
                    .dropdown-item {
                        color: $mainColor;
                        font-family: "ProximaNovaExtrabold";
                        transition: all .3s;
                        padding: 6px;
                        border-radius: 4px;
                        text-align: start;
                        &:hover {
                            background-color: $mainColor;
                            color: #000000;
                        }
                        span {
                            font-family: "ProximaNovaRegular";
                        }
                        &.lang {
                            font-family: "Simah-Medium";
                        }
                    }
                    .dropdown-divider {
                        margin: 0;
                        border-color: #ffdd004f;
                    }
                }
            }
        }
    }
}

/* Slide transitions */
.slideUp {
    transform: translateY(-100px);
    transition: transform .5s ease;
}
.slideDown {
    transform: translateY(0);
    transition: transform .5s ease;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
}


.hamburger span, .hamburger span:before, .hamburger span:after {
    cursor: pointer;
    border-radius: 1px;
    height: 3px;
    width: 35px;
    background: #225AD5;
    position: absolute;
    display: inline-block;
    content: '';
  }
  .hamburger span:before {
    top: -10px; 
  }
  .hamburger span:after {
    bottom: -10px;
  }
  /*
  #hamburger span, #hamburger span:before, #hamburger span:after {
    transition: all 200ms ease-in-out;
  }
  */
  .hamburger span, .hamburger span:before, .hamburger span:after {
  -webkit-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1.000); 
     -moz-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1.000); 
       -o-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1.000); 
          transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1.000); /* easeInOutCubic */
  }
  .hamburger.active span {
    background-color: transparent;
  }
  .hamburger.active span:before, .hamburger.active span:after {
    top: 0;
  }
  .hamburger.active span:before {
    transform: rotate(45deg);
  }
  .hamburger.active span:after {
    transform: translateY(-10px) rotate(-45deg);
    top: 10px;
  }