.checkout-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        padding: 4rem 0;
        background-color: #fff;
        article {
            .checkout-content {
                //** Start Payment Info Section **//
                .cart-info {
                    background-color: #F4F4F4;
                    border-radius: 25px;
                    padding: 25px;
                    header {
                        h3 {
                            @media(max-width: 600px) {
                                font-size: 20px;
                            }
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 25px;
                            margin-bottom: 20px;
                        }
                        h6 {
                            color: #231F20;
                            font-family: "Poppins-Medium";
                            margin-bottom: 10px;
                        }
                    }
                    article {
                        form {
                            input {
                                background-color: #fff;
                                border: none;
                                outline: none;
                                font-family: "ProximaNovaRegular";
                                border-radius: 32px;
                                padding: 10px 25px;
                            }
                            .form-control:focus{
                                box-shadow: none;
                            }
                            ::placeholder {
                                color: #C4C6CA;
                            }
                            p {
                                color: #6D6E70;
                                font-weight: 600;
                                margin: 20px 0;
                            }
                            .upload-file {
                                margin-bottom: 3rem;
                                .file-input {
                                    text-align: start !important;
                                    white-space: nowrap;
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                    width: 100%;
                                    font-family: sans-serif;
                                    display: inline-block;
                                    text-align: left;
                                    padding: 8px 0;
                                    position: relative;
                                    width: 100%;
                                }
                                .file-input > [type="file"] {
                                    position: absolute;
                                    top: 0;
                                    left: 0;
                                    width: 100%;
                                    height: 100%;
                                    opacity: 0;
                                    z-index: 10;
                                    cursor: pointer;
                                }
                                .file-input > .button {
                                    display: inline-block;
                                    cursor: pointer;
                                    padding: 1px 16px;
                                    border-radius: 25px;
                                    margin-right: 8px;
                                    font-size: 20px;
                                    font-weight: bold;
                                    color: #000000;
                                    background-color: $mainColor;
                                    border: 3px solid $mainColor;
                                    outline: none;
                                    font-family: "BebasNeueBold";
                                }
                                .file-input > .label {
                                    color: #6D6E70;
                                    font-family: "Poppins-Light";
                                }
                                #preview {
                                    max-width: 180px;
                                    max-height: 160px;
                                    margin-top: 16px;
                                    object-fit: cover;
                                }
                                span.button{
                                    img {
                                        width: 18px;
                                        height: 18px;
                                        margin-right: 5px;
                                    }
                                }
                            }
                            .actions {
                                .coupon {
                                    display: flex;
                                    margin-top: 2em;
                                    input {
                                        max-width: 150px;
                                    }
                                    button {
                                        border-radius: 32px;
                                        height: 38px;
                                        width: 125px;
                                        background: #25A7DE;
                                        border: none;
                                        color: #fff;
                                        font-weight: 500;
                                        outline: none;
                                        transition: all .3s;
                                        &:hover {
                                            background-color: #2E7491;
                                        }
                                    }
                                }
                                .check {
                                    button {
                                        background-color: #37B14A;
                                        font-family: "Poppins-Bold";
                                        color: #fff;
                                        width: 100%;
                                        min-height: 55px;
                                        border-radius: 25px;
                                        border: 2px solid #37B14A;
                                        font-size: 22px;
                                        outline: none;
                                        margin-top: 2em;
                                        -webkit-transition: all .3s;
                                        transition: all .3s;
                                        &:hover {
                                            background-color: #258734;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                //** End Payment Info Section **//
            }
        }
    }
}