@keyframes scrollTop {
    0% {
        opacity: .2;
        bottom: 0;
    }

    25% {
        opacity: .5;
        bottom: 20%;
    }

    50% {
        opacity: .7;
        bottom: 35%;
    }

    75% {
        opacity: 1;
        bottom: 45%;
    }

    100% {
        opacity: 0;
        bottom: 60%;
    }
}