.portofolio-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding-top: 4rem;
        article {
            //** Start Topics Search **//
            .topic-search {
                header {
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                        margin-bottom: 5px;
                    }
                }
                article {
                    p {
                        color: #404041;
                        font-size: 18px;
                        font-family: "Poppins-Regular";
                    }
                }
            }
            //** End Topics Search **//

            //** Start Filter Section **//
            .filter-sec {
                header {
                    border-bottom: 2px solid #E4E5E6;
                    padding-bottom: 15px;
                    h3, a {
                        font-family: "Poppins-SemiBold";
                        text-decoration: none;
                        color: #000000;
                        span {
                            margin: 0 10px;
                            font-size: 22px;
                        }
                    }
                }   
                article {
                    .filter-model {
                        .modal-header {
                            header {
                                border: none;
                                padding: 0;
                                h3 {
                                    margin: 0;
                                    font-size: 18px;
                                }
                            }
                            button {
                                font-family: "Poppins-Bold";
                                color: #000000;
                                opacity: 1;
                                font-size: 18px;
                            }
                        }
                        .modal.modal-left .modal-dialog, .modal.modal-right .modal-dialog {
                            max-width: 380px;
                            min-height: calc(100vh - 0px);
                        }
                        .modal.modal-left.show .modal-dialog, .modal.modal-right.show .modal-dialog {
                            transform: translate(0, 0);
                        }
                        .modal.modal-left .modal-content, .modal.modal-right .modal-content {
                            height: calc(100vh - 0px);
                            overflow-y: auto;
                        }
                        .modal.modal-left .modal-dialog {
                            transform: translate(-100%, 0);
                            margin: 0px auto 0 0;
                        }
                        .modal.modal-right .modal-dialog {
                            transform: translate(100%, 0);
                            margin: 0px 0 0 auto;
                        }
                        .modal-full {
                            min-width: 100%;
                            margin: 0;
                        }
                        
                        .modal-full .modal-content {
                            min-height: 100vh;
                        }
                    }
                    ul {
                        border-bottom: 2px solid #E4E5E6;
                        padding: 20px 0 !important;
                        &:last-child {
                            border: none;
                        }
                        li {
                            font-family: "Poppins-Medium";
                            color: #000000;
                            h4 {
                                color: #808082;
                                font-size: 17px;
                                margin-bottom: 15px;
                            }
                            /* The container */
                            .container-checkbox {
                                display: block;
                                position: relative;
                                padding-left: 35px;
                                margin-bottom: 12px;
                                cursor: pointer;
                                font-size: 16px;
                                -webkit-user-select: none;
                                -moz-user-select: none;
                                -ms-user-select: none;
                                user-select: none;
                            }

                            /* Hide the browser's default checkbox */
                            .container-checkbox input {
                                position: absolute;
                                opacity: 0;
                                cursor: pointer;
                            }

                            /* Create a custom checkbox */
                            .container-checkbox .checkmark {
                                position: absolute;
                                top: 0;
                                left: 0;
                                height: 25px;
                                width: 25px;
                                background-color: #eee;
                            }

                            /* On mouse-over, add a grey background color */
                            .container-checkbox:hover input ~ .checkmark {
                                background-color: #ccc;
                            }

                            /* When the checkbox is checked, add a blue background */
                            .container-checkbox input:checked ~ .checkmark {
                                background-color: #000000;
                                border-radius: 4px;
                            }

                            /* Create the checkmark/indicator (hidden when not checked) */
                            .container-checkbox .checkmark:after {
                                content: "";
                                position: absolute;
                                display: none;
                            }

                            /* Show the checkmark when checked */
                            .container-checkbox input:checked ~ .checkmark:after {
                                display: block;
                            }

                            /* Style the checkmark/indicator */
                            .container-checkbox .checkmark:after {
                                left: 9px;
                                top: 3px;
                                width: 8px;
                                height: 14px;
                                border: solid #ffdd00;
                                border-width: 0 3px 3px 0;
                                transform: rotate(45deg);
                            }

                        }
                    }
                }
            }
            //** End Filter Section **//
        }

        //** Start Topics Card **//
        .topics-cards {
            a {
                text-decoration: none;
            }
            .topic-card {
                background-color: #000000;
                border-radius: 11px;
                overflow: hidden;
                &:hover {
                    .card-img {
                        img {
                            transform: scale(1.05);
                        }
                    }
                }
                .card-img {
                    overflow: hidden;
                    position: relative;
                    img {
                        transition: all .4s ease-in-out;
                    }
                    span {
                        position: absolute;
                        right: 0;
                        top: 20px;
                        padding: 2px 5px;
                        border-radius: 7px 0px 0px 7px;
                        font-family: "Poppins-Bold";
                        font-size: 13px;
                        &.black-sale {
                            background: #000000;
                            color: $mainColor;
                        }
                        &.red-sale {
                            background: #D1232A;
                            color: #fff;
                        }
                    }
                }
                .card-body {
                    color: #fff;
                    padding: 10px 20px;
                    time {
                        font-family: "Poppins-Bold";
                        font-size: 14px;
                    }
                    h4 {
                        font-family: "Poppins-Bold";
                        font-size: 18px;
                        margin: 7px 0;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    p {
                        font-size: 15px;
                        font-family: "Poppins-Regular";
                        margin: 0;
                    }
                }
            }
        }
        //** End Topics Card **//
    }
    .pagination {
        justify-content: center;
        margin-top: 2em;
        flex-wrap: wrap;
        li {
            margin: 5px;
            &.previous {
                a {
                    border-radius: 24px;
                    background: #E4E5E6;
                    color: #6D6E70;
                    font-weight: 500;
                    min-width: 122px;
                    text-align: center;
                    transition: all .3s;
                    &.active,
                    &:hover {
                        background: #CED0D1;
                        color: #6D6E70;
                        border-radius: 47px;
                        border-color: #CED0D1;
                    }
                }
            }
            &.next {
                a {
                    border-radius: 24px;
                    background: $mainColor;
                    color: #000000;
                    font-weight: 500;
                    min-width: 122px;
                    text-align: center;
                    transition: all .3s;
                    &.active,
                    &:hover {
                        background: #000;
                        color: $mainColor;
                        border-radius: 47px;
                        border-color: #000;
                    }
                }
            }
            a {
                border-radius: 47px;
                border: 1px solid #E4E5E6;
                color: #8f8f8f;
                font-family: "Poppins-Regular";
                &.active,
                &:hover {
                    background-color: $mainColor;
                    color: #000000;
                    border-radius: 47px;
                    border: 1px solid $mainColor;
                }
                &:focus {
                    box-shadow: none;
                }
            }
        }
    }
}