.order-service {
    background-color: $mainColor;
    .brand-section {
        padding: 8rem 0;
        button {
            @media(max-width: 600px) {
                font-size: 18px;
                min-height: 50px;
                max-width: 230px;
            }
            background: black;
            color: #fff;
            font-size: 35px;
            padding: 8px;
            width: 100%;
            max-width: 380px;
            border-radius: 70px;
            min-height: 72px;
            border: 3px solid black;
            outline: none;
            font-family: "Poppins-Bold";
            transition: all .3s;
            &:hover {
                background-color: $mainColor;
                color: #000000;
            }
        }
        article {
            p {
                margin: 0;
                color: #000000;
                font-size: 22px;
                margin-top: 20px;
                position: relative;
                font-family: "Poppins-ExtraLight";
                @media(max-width: 448px){
                    font-size: 15px;
                }
                &:before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    top: 50px;
                    bottom: 0;
                    height: 3px;
                    width: 124px;
                    background: #000000;
                    -webkit-transform: translateX(-50%);
                    transform: translateX(-50%);
                }
            }
        }
    }
}