.order-placed-page {
    background-color: #000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        article {
            //** Start Step Indicator **//
            .step-parent {
                padding: 2rem 0;
                box-shadow: 0px 12px 13px #E5E5E5BF;
                .step-indicator {
                    display: flex;
                    align-items: center;
                    max-width: 420px;
                    margin: auto;
                    height: 30px;
                    .step {
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        position: relative;
                        z-index: 1;
                        .step-icon {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            height: 30px;
                            width: 30px;
                            border-radius: 50%;
                            background: #c2c2c259;
                            font-size: 10px;
                            text-align: center;
                            color: #000;
                            position: relative;
                            font-size: 16px;
                            font-family: "Poppins-Bold";
                        }
                        &.active .step-icon {
                            background: $mainColor;
                        }
                        &.finished {
                            .step-icon {
                                background-color: #fff;
                                border: 3px solid $mainColor;
                                img {
                                    width: 26px;
                                    position: relative;
                                    top: -3px;
                                    left: 7px;
                                }
                            }
                        }
                    }
                    .indicator-line {
                        width: 100%;
                        height: 3px;
                        background: #c2c2c259;
                        flex: 1;
                        &.active {
                            background: $mainColor;
                        }
                    }
                }
            }
            //** Start Revision Section **//
            .revision-section {
                header {
                    margin: 3rem 0;
                    text-align: center;
                    h3 {
                        @media(max-width: 600px) {
                            font-size: 22px;
                        }
                        margin-bottom: 1rem;
                        color: #000000;
                        font-size: 45px;
                        font-family: "Poppins-Bold";
                    }
                    h5 {
                        @media(max-width: 600px) {
                            font-size: 17px;
                        }
                        color: #000000;
                        font-family: "Poppins-SemiBold";
                        font-size: 25px;
                    }
                }
                article {
                    .edit-action {
                        white-space: nowrap;
                        margin-left: 5%;
                        button {
                            all: unset;
                            cursor: pointer;
                            span {
                                color: #A4A6A9;
                                font-family: "Poppins-Medium";
                                font-size: 18px;
                                transition: all .3s;
                                display: inline-block;
                            }
                            &:hover {
                                span {
                                    transform: translateX(5px);
                                }
                            }
                        }
                    }
                    .order-name {
                        display: flex;
                        .form-group {
                            width: 90%;
                            label {
                                @media(max-width: 600px) {
                                    font-size: 15px;
                                }
                                color: #000000;
                                font-family: "Poppins-SemiBold";
                                font-size: 18px;
                            }
                            input {
                                background: #F6F8FA;
                                border-radius: 40px;
                                border: none;
                                height: 45px;
                                font-family: "Poppins-Regular";
                                &:focus {
                                    box-shadow: none;
                                }
                            }
                        }
                    }
                    .desired-service {
                        display: flex;
                        margin: 25px 0;
                        .content {
                            width: 90%;
                            h5 {
                                @media(max-width: 600px) {
                                    font-size: 15px;
                                }
                                color: #000000;
                                font-family: "Poppins-SemiBold";
                                font-size: 18px;
                                margin-bottom: 20px;
                            }
                            ul {
                                display: flex;
                                justify-content: space-between;
                                flex-wrap: wrap;
                                li {
                                    color: #000000;
                                    font-family: "Poppins-Medium";
                                    font-size: 15px;
                                    margin-right: 20px;
                                }
                            }
                        }
                    }
                    .questions {
                        display: flex;
                        margin: 45px 0;
                        .content {
                            width: 90%;
                            h5 {
                                @media(max-width: 600px) {
                                    font-size: 15px;
                                }
                                color: #000000;
                                font-family: "Poppins-SemiBold";
                                font-size: 18px;
                                margin-bottom: 40px;
                            }
                            label {
                                color: #000000;
                                font-family: "Poppins-Medium";
                                font-size: 15px;
                            }
                            input {
                                background: #F6F8FA;
                                border-radius: 40px;
                                border: none;
                                height: 45px;
                                font-family: "Poppins-Regular";
                                &:focus {
                                    box-shadow: none;
                                }
                            }
                        }
                    }
                    .styles  {
                        display: flex;
                        margin: 45px 0;
                        .content {
                            width: 90%;
                            h5 {
                                @media(max-width: 600px) {
                                    font-size: 15px;
                                }
                                color: #000000;
                                font-family: "Poppins-SemiBold";
                                font-size: 18px;
                                margin-bottom: 40px;
                            }
                            .style-content {
                                position: relative;
                                border: 3px solid $mainColor;
                                border-radius: 14px;
                                max-width: 300px;
                                height: 200px;
                                padding: 2.5rem;
                                img {
                                    width: 100%;
                                    height: 100%;
                                }
                                .checked-icon {
                                    position: absolute;
                                    top: 0px;
                                    right: 7px;
                                    img {
                                        opacity: 1;
                                        transition: all .3s;
                                        width: 20px;
                                        height: 20px;
                                    }
                                }
                            }
                        }
                    }
                    .addtional-info  {
                        display: flex;
                        margin: 45px 0;
                        .content {
                            width: 90%;
                            h5 {
                                @media(max-width: 600px) {
                                    font-size: 15px;
                                }
                                color: #000000;
                                font-family: "Poppins-SemiBold";
                                font-size: 18px;
                                margin-bottom: 40px;
                            }
                            label {
                                color: #000000;
                                font-family: "Poppins-Medium";
                                font-size: 15px;
                            }
                            textarea {
                                border: 3px solid #FFDD00;
                                border-radius: 14px;
                                font-family: "Poppins-Regular";
                                &:focus {
                                    box-shadow: none;
                                }
                            }
                        }
                    }
                    .preview-media-btn {
                        text-align: end;
                        button {
                            color: #6D6E70;
                            all: unset;
                            cursor: pointer;
                            font-family: "Poppins-Light";
                            font-size: 16px;
                        }
                    }
                    .action-btns {
                        margin: 4rem 0;
                        text-align: center;
                        button {
                            @media(max-width: 600px) {
                                font-size: 16px;
                                min-width: 140px;
                                margin: 5px;
                            }
                            &:first-child {
                                background-color: $mainColor;
                                color: #000;
                                border-color: $mainColor;
                                &:hover {
                                    background-color: #000;
                                    color: #FFDD00;
                                    border-color: #000;
                                }
                            }
                            margin: 10px;
                            cursor: pointer;
                            background: #37B14A;
                            border: none;
                            border: 2px solid #37B14A;
                            color: #fff;
                            padding: 3px 5px;
                            min-width: 185px;
                            border-radius: 25px;
                            outline: none;
                            font-size: 20px;
                            font-family: "Poppins-Bold";
                            transition: all .3s;
                            &:last-child {
                                &:hover {
                                    background-color: #258734;
                                    border-color: #258734;
                                }
                            }
                        }
                    }
                }
            }
            //** End Revision Section **//
        }
    }
}