.footer-section{
    background-color: #000000;
    overflow: hidden;
    .footer-links {
        .content {
            padding: 60px 0;
            .logo {
                img {
                    width: 110px;
                    margin: 20px 0;
                }
            }
            .address {
                ul {
                    margin: 0;
                    li {
                        color: #fff;
                        font-family: "Poppins-SemiBold";
                        font-size: 13px;
                        a {
                            text-decoration: none;
                            color: $mainColor;
                            font-family: "Poppins-SemiBold";
                        }
                    }
                }
            }
            .links {
                ul {
                    li {
                        margin-bottom: 5px;
                        a {
                            text-decoration: none;
                            font-size: 14px;
                            color: #fff;
                            font-family: "Poppins-SemiBold";
                            transition: .3s;
                            &:hover {
                                color: $mainColor;
                            }
                        }
                    }
                }
            }
        }
        .copywright {
            p{
                color: #fff;
                margin-bottom: 18px;
                font-family: "Poppins-SemiBold";
                font-size: 13px;
            }
        }
    }
    .social-icons {
        background: linear-gradient(90deg, #ffffff 50%, #ffdd00 50%);
        @media(max-width: 992px) {
            background: #fff !important;
        }
        padding: 5px 0;
        @media(max-width: 992px) {
            padding: 0;
        }
        .container {
            @media(max-width: 992px) {
                padding: 0;
                text-align: center;
                max-width: 100%
            }
        }
        .contetnt {
            .left-side {
                @media(max-width: 992px) {
                    background: black !important;
                    padding: 25px 0;
                }
                ul {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    @media(max-width: 992px) {
                        justify-content: center;
                        
                    }
                    li {
                        @media(max-width: 992px) {
                            padding-right: 3px;
                            font-size: 12px;
                            margin: 5px 10px;
                            
                        }
                        text-transform: uppercase;
                        font-size: 15px;
                        margin: 5px;
                        padding-right: 5%;
                        a {
                            @media(max-width: 992px) {
                                color: #fff;
                            }
                            &.dropdown-item {
                                @media(max-width: 992px) {
                                    color: #000;
                                }
                            }
                            color: #000;
                            font-family: "Poppins-SemiBold";
                            text-decoration: none;
                        }
                        .dropdown {
                            .dropdown-toggle {
                                @media(max-width: 992px) {
                                    color: #fff;
                                }
                                display: inline-block;
                                text-decoration: none;
                                color: #000;
                                font-family: "Poppins-SemiBold";
                            }
                            .dropdown-toggle::after{
                                font-family: "Font Awesome 5 Free";
                                content: "\f078";
                                display: inline-block;
                                padding-right: 3px;
                                vertical-align: middle;
                                font-weight: 900;
                                border: none;
                            }
                            .dropdown-menu {
                                // background-color: #000000;
                                // padding: 0;
                                // overflow: hidden;
                                // border: 2px solid #FFDD00;
                                padding: 10px 15px;
                                border: 2px solid #000000;
                                &::before {
                                    @media(max-width: 992px) {
                                        content: '';
                                    }
                                    font-family: "Font Awesome 5 Free";
                                    content: "\f078";
                                    display: inline-block;
                                    font-weight: 900;
                                    color: #000000;
                                    position: absolute;
                                    bottom: -16px;
                                    left: 19px;
                                    background: #fff;
                                }
                                &.show {
                                    top: -10px !important;
                                }
                                .dropdown-item {
                                    // color: $mainColor;
                                    font-family: "ProximaNovaExtrabold";
                                    transition: all .3s;
                                    padding: 10px 5px;
                                    border-radius: 4px;
                                    font-size: 12px;
                                    text-align: start;
                                    &:hover {
                                        background-color: $mainColor !important;
                                        color: #000000;
                                    }
                                    span {
                                        font-family: "ProximaNovaRegular";
                                    }
                                }
                                .dropdown-divider {
                                    margin: 0;
                                    // padding: 5px 0;
                                    border-color: #5353534f;
                                }
                            }
                        }
                    }
                }
            }
            .right-side {
                @media(max-width: 992px) {
                    background: $mainColor !important;
                    padding: 10px 0;
                    ul {
                        li {
                            a {
                                color: #000 !important;
                            }
                        }
                    }
                }
                ul.social {
                    margin: 0;
                    li {
                        @media(max-width: 490px){
                            margin: 3px;
                            padding: 0;
                        }
                        display: inline-block;
                        margin: 5px;
                        padding-right: 2%;
                        font-size: 18px;
                        a {
                            text-decoration: none;
                            color: #000000;
                        }
                    }
                }
            }
        }
    }
}

.scrollTop {
    cursor: pointer;
    display: none;
    position: fixed;
    right: 20px;
    bottom: 127px;
    z-index: 999;
    &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        content: '';
        width: 100%;
        height: 80%;
    }
    span {
        border-radius: 41px;
        background: #fff;
        display: inline-block;
        text-align: center;
        width: 50px;
        height: 60px;       
        box-sizing: border-box;
        padding: 0 6px;
        transition: all .3s ease-in-out;
        position: relative;
        img {
            position: absolute;
            left: 10px;
            bottom: 0;
            width: 30px;
            height: 30px;
            animation: scrollTop 1.4s linear infinite;
        }
    }
}