.store-item-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding-top: 4rem;
        article {
            //** Start Topics Search **//
            .topic-search {
                header {
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                        margin-bottom: 5px;
                    }
                }
                article {
                    p {
                        color: #404041;
                        font-size: 18px;
                        font-family: "Poppins-Regular";
                    }
                    form {
                        margin-top: 30px;
                        font-family: "Poppins-Medium";
                        input {
                            background: #E4E5E6;
                            border-radius: 32px;
                            border: none;
                            padding-left: 18px;
                            padding-right: 18px;
                            &:focus {
                                box-shadow: none;
                            }
                        }
                        button {
                            background-color: $mainColor;
                            border-radius: 32px;
                            min-width: 45px;
                            border: none;
                            outline: none;
                            transition: all .3s;
                            &:hover {
                                transform: scale(1.05) translateY(-5px);
                            }
                            img {
                                width: 20px;
                            }
                        }
                    }
                }
            }
            //** End Topics Search **//

            //** Start Logo Details Section **//
            .logo-details {
                margin-top: 4rem;
                .logo-info {
                    header {
                        h3 {
                            @media(max-width: 600px){
                                font-size: 18px;
                            }
                            font-family: "Poppins-Bold";
                            font-size: 23px;
                            margin-bottom: 15px;
                        }
                    }
                    article {
                        p {
                            color: #404041;
                            font-family: "Poppins-Light";
                        }
                        time {
                            color: #404041;
                            font-family: "Poppins-Light";
                            margin: 20px 0;
                            display: block;
                        }
                        ul {
                            display: flex;
                            flex-wrap: wrap;
                            margin-bottom: 20px;
                            li {
                                @media(max-width: 600px){
                                    font-size: 10px;
                                }
                                color: #909295;
                                border: 2px solid #CED0D1;
                                border-radius: 5px;
                                font-family: "Poppins-Regular";
                                margin-right: 5px;
                                margin-bottom: 5px;
                                padding: 5px 10px;
                                font-size: 14px;
                                &.ai {
                                    border-color: #F4901E;
                                    color: #F4901E;
                                }
                            }
                        }
                        button {
                            font-family: "Poppins-SemiBold";
                            background-color: $mainColor;
                            min-width: 180px;
                            border-radius: 24px;
                            border: none;
                            padding: 7px 5px;
                            outline: none;
                            margin-top: 20px;
                            img {
                                width: 20px;
                            }
                            transition: all .3s;
                            &:hover {
                                transform: scale(1.05) translateY(-5px);
                            }
                        }
                    }
                }
                .logo-more-info {
                    margin-top: 3rem;
                    color: #6D6E70;
                    font-family: "Poppins-Regular";
                    strong {
                        font-family: "Poppins-Bold";
                        color: #404041;
                    }
                    h3 {
                        margin-top: 35px;
                        color: #404041;
                        @media(max-width: 600px){
                            font-size: 18px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 23px;
                        margin-bottom: 10px;
                    }
                    ul {
                        margin-left: 20px !important;
                    }   
                }
            }
            //** End Logo Details Section **//
        }

        //** Start Topics Card **//
        .topics-cards {
            background-color: #F4F4F4;
            padding: 4rem 0;
            header {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-bottom: 2rem;
                h3 {
                    @media(max-width: 600px){
                        font-size: 20px;
                    }
                    font-family: "Poppins-Bold";
                    font-size: 30px;
                    margin-bottom: 5px;
                }
                a {
                    font-family: "Poppins-SemiBold";
                    font-size: 16px;
                    margin-bottom: 5px;
                    color: #000000;
                    text-decoration: none;
                    transition: all .3s;
                    &:hover {
                        transform: translateX(5px)
                    }
                }
            }
            a {
                text-decoration: none;
            }
            .topic-card {
                background-color: #000000;
                border-radius: 11px;
                overflow: hidden;
                &:hover {
                    .card-img {
                        img {
                            transform: scale(1.05);
                        }
                    }
                }
                .card-img {
                    overflow: hidden;
                    position: relative;
                    img {
                        transition: all .4s ease-in-out;
                    }
                    span {
                        position: absolute;
                        right: 0;
                        top: 20px;
                        padding: 2px 5px;
                        border-radius: 7px 0px 0px 7px;
                        font-family: "Poppins-Bold";
                        font-size: 13px;
                        &.black-sale {
                            background: #000000;
                            color: $mainColor;
                        }
                        &.red-sale {
                            background: #D1232A;
                            color: #fff;
                        }
                    }
                }
                .card-body {
                    color: #fff;
                    padding: 10px 20px;
                    time {
                        font-family: "Poppins-Bold";
                        font-size: 14px;
                    }
                    h4 {
                        font-family: "Poppins-Bold";
                        font-size: 18px;
                        margin: 7px 0;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    p {
                        font-size: 15px;
                        font-family: "Poppins-Regular";
                        margin: 0;
                    }
                }
            }
        }
        //** End Topics Card **//
    }
}