.checkout-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        padding: 4rem 0;
        background-color: #fff;
        article {
            .checkout-content {
                //** Start Orders Cart Section **//
                .order-cart {
                    header {
                        h2 {
                            @media(max-width: 600px) {
                                font-size: 20px;
                            }
                            font-size: 25px;
                            color: #000000;
                            font-family: "Poppins-Bold";
                        }
                    }
                    article {
                        margin-top: 1rem;
                        .order-table {
                            table {
                                white-space: nowrap;
                                thead {
                                    tr {
                                        th {
                                            color: #231F20;
                                            font-family: "Poppins-Bold";
                                        }
                                    }
                                    border-bottom: 3px solid #E4E5E6;
                                }
                                tbody {
                                    border-bottom: 3px solid #E4E5E6;
                                    tr {
                                        &:first-child {
                                            td {
                                                padding-top: 35px;
                                            }
                                        }
                                        &:last-child {
                                            td {
                                                padding-bottom: 35px;
                                            }
                                        }
                                    }
                                    tr {
                                        td {
                                            color: #909295;
                                            font-size: 14px;
                                            font-family: "Poppins-Medium";
                                            span {
                                                &.ready {
                                                    color: #37B14A;
                                                }
                                                &.qeued {
                                                    color: #F4901E;
                                                }
                                            }
                                            button {
                                                all: unset;
                                                cursor: pointer;
                                                color: #909295;
                                                font-family: "Poppins-Bold";
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        .order-action {
                            .add-service-btn {
                                button {
                                    img {
                                        width: 20px;
                                        transition: all .3s ease;
                                    }
                                    background: $mainColor;
                                    border: none;
                                    padding: 4px 5px;
                                    min-width: 165px;
                                    border-radius: 25px;
                                    outline: none;
                                    font-family: "Poppins-SemiBold";
                                    &:hover {
                                        img {
                                            transform: translateX(-5px);
                                        }
                                    }
                                }
                            }
                            table {
                                tbody {
                                    border-bottom: 3px solid #E4E5E6;
                                    tr {
                                        td {
                                            color: #909295;
                                            font-family: "Poppins-SemiBold";
                                            span {
                                                font-family: "Poppins-Medium";
                                                font-size: 13px;
                                            }
                                        }
                                    }
                                }
                                tfoot {
                                    tr {
                                        td {
                                            color: #909295;
                                            font-family: "Poppins-SemiBold";
                                            font-size: 20px;
                                            span {
                                                font-family: "Poppins-Medium";
                                                font-size: 17px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                //** End Orders Cart Section **//
                //** Start Payment Info Section **//
                .payment-info {
                    background-color: #F4F4F4;
                    border-radius: 25px;
                    padding: 25px;
                    header {
                        h3 {
                            @media(max-width: 600px) {
                                font-size: 20px;
                            }
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 25px;
                            margin-bottom: 20px;
                        }
                        h6 {
                            color: #231F20;
                            font-family: "Poppins-Medium";
                            margin-bottom: 10px;
                        }
                    }
                    article {
                        .tabs-section {
                            ul {
                                li.nav-item {
                                    margin-bottom: 10px;
                                    a {
                                        display: flex;
                                        flex-direction: column;
                                        align-items: center;
                                        justify-content: center;
                                        font-family: "Poppins-Medium";
                                        border: 2px solid #909295;
                                        color: #909295;
                                        width: 90px;
                                        margin-right: 5px;
                                        font-size: 13px;
                                        overflow: hidden;
                                        text-align: center;
                                        line-height: 14px;
                                        height: 100%;
                                        transition: all .3s;
                                        padding: 16px 6px;
                                        &.active {
                                            background-color: $mainColor;
                                            border-color: $mainColor;
                                            color: #231F20;
                                            img {
                                                filter: unset;
                                            }
                                        }
                                        &:hover {
                                            background-color: $mainColor;
                                            border-color: $mainColor;
                                            color: #231F20;
                                            img {
                                                filter: unset;
                                            }
                                        }
                                        img {
                                            margin-bottom: 10px;
                                            filter: opacity(0.5);
                                            transition: all .3s;
                                        }
                                    }
                                }
                            }
                            .tab-content {
                                .credit-card-tab, .sepa-section {
                                    label {
                                        color: #A4A6A9;
                                        font-size: 13px;
                                        font-family: "Poppins-Medium";
                                    }
                                    .name {
                                        .form-group {
                                            input {
                                                color: #000000;
                                                font-size: 13px;
                                                font-family: "Poppins-Regular";
                                                border: none;
                                                background-color: transparent;
                                                outline: none;
                                                display: block;
                                                width: 100%;
                                            }
                                        }
                                    }
                                    .number {
                                        .credit-inputs {
                                            display: flex;
                                            flex-wrap: wrap;
                                            input {
                                                width: 40px;
                                                margin-right: 10px;
                                                margin-bottom: 10px;
                                                color: #000000;
                                                font-size: 13px;
                                                font-family: "Poppins-Regular";
                                                border: none;
                                                background-color: transparent;
                                                outline: none;
                                            }
                                        }
                                    }
                                    .more-info {
                                        display: flex;
                                        flex-wrap: wrap;
                                        .expiration-date {
                                            margin-right: 10%;
                                        }
                                        .form-group {
                                            select {
                                                color: #A4A6A9;
                                                border: none;
                                                background-color: transparent;
                                                width: 50px;
                                                &:first-child {
                                                    margin-right: 15px;
                                                }
                                                &:last-child {
                                                    width: 80px;
                                                }
                                            }
                                            input {
                                                width: 40px;
                                                margin-right: 10px;
                                                margin-bottom: 10px;
                                                color: #000000;
                                                font-size: 13px;
                                                font-family: "Poppins-Regular";
                                                border: none;
                                                background-color: transparent;
                                                outline: none;
                                                display: block;
                                            }
                                        }
                                    }
                                }
                                .paypal-section, .klarna-section, .invoice-section  {
                                    padding: 2rem 0;
                                    h5 {
                                        color: #000000;
                                        font-size: 15px;
                                        font-family: "Poppins-Bold";
                                    }
                                    p {
                                        color: #A4A6A9;
                                        font-size: 14px;
                                        font-family: "Poppins-Medium";
                                    }
                                }
                            }
                        }
                        .payment-footer {
                            margin-top: 1rem;
                            label {
                                color: #000000;
                                font-family: "Poppins-Regular";
                                font-size: 15px;
                                a {
                                    font-family: "Poppins-Bold";
                                    text-decoration: underline;
                                    &:first-child {
                                        text-decoration: none;
                                    }
                                    color: #25A7DE;
                                    strong {
                                        color: #000000;
                                    }
                                }
                            }
                            .action-btn {
                                margin-top: 2rem;
                                button {
                                    @media(max-width: 600px) {
                                        font-size: 18px;
                                        min-height: 45px;
                                    }
                                    background-color: #37B14A;
                                    font-family: "Poppins-Bold";
                                    color: #fff;
                                    width: 100%;
                                    min-height: 55px;
                                    border-radius: 25px;
                                    border: 2px solid #37B14A;
                                    font-size: 22px;
                                    outline: none;
                                    transition: all .3s;
                                    &:hover {
                                        background-color: #258734;
                                    }
                                }
                            }
                        }
                    }
                }
                //** End Payment Info Section **//
            }
        }
    }
}