.edit-profile-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        padding: 4rem 0;
        background-color: #fff;
        article {
            form {
                h3 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    color: #000000;
                    font-family: "Poppins-Bold";
                    font-size: 30px;
                    margin-top: 25px;
                }
                .custom-control-inline {
                    margin-right: 10%;
                }
                .custom-radio {
                    position: relative;
                    padding-left: 0rem !important;
                    .custom-control-input:focus~.custom-control-label::before {
                        box-shadow: 0 0 0 0.2rem #ffdd0066;
                    }
                    input[type="radio"] {
                        opacity: 0;
                    }
                    label {
                        padding-left: 2rem;
                        cursor: pointer;
                        margin-bottom: 7px !important;
                        &:before {
                            width: 18px;
                            height: 18px;
                            position: absolute;
                            top: 1px;
                            left: 0;
                            content: '';
                            display: inline-block;
                            -moz-border-radius: 50px;
                            -webkit-border-radius: 50px;
                            border-radius: 50px;
                            border: 1px solid #A4A6A9;
                            background: #fff;
                        }
                    }
                    input[type="radio"] {
                        margin:0px;
                        &:disabled + label {
                            color: #999;
                            &:before {
                            background-color: #A4A6A9;
                            }
                        }
                        &:checked + label::after {
                            content: '';
                            position: absolute;
                            top: 5px;
                            left: 4px;
                            display: inline-block;
                            font-size: 11px;
                            width: 10px;
                            height: 10px;
                            background-color: #FFDD00;
                            -moz-border-radius: 50px;
                            -webkit-border-radius: 50px;
                            border-radius: 50px;
                        }
                    }
                }
                label {
                    color: #A4A6A9;
                    font-family: "Poppins-Medium";
                }
                input, select {
                    background: #F6F8FA;
                    border-radius: 40px;
                    border: none;
                    height: 45px;
                    font-family: "Poppins-Regular";
                    &:focus {
                        box-shadow: none;
                    }
                }
                select {
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    appearance: none;
                    background: url('http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png') #F6F8FA no-repeat calc(100% - 15px);
                }
                ::placeholder {
                    color: #C4C6CA;
                }
                .form-actions {
                    text-align: end;
                    margin-top: 2rem;
                    button {
                        @media(max-width: 600px) {
                            min-width: 140px;
                        }
                        margin: 5px;
                        transition: all .3s;
                        font-family: "Poppins-SemiBold";
                        font-size: 15px;
                        outline: none;
                        border-radius: 25px;
                        padding: 5px;
                        min-width: 160px;
                        min-height: 40px;
                        outline: none;
                        transition: all .3s;
                        &:first-child {
                            background-color: #E4E5E6;
                            border: 2px solid transparent;
                            color: #000000;
                            &:hover {
                                background-color: #CED0D1;
                            }
                        }
                        &:last-child {
                            background-color: #37B14A;
                            border: 2px solid transparent;
                            color: #fff;
                            &:hover {
                                background-color: #258734;
                            }
                        }
                    }
                }
            }
        }
    }
}