.edit-order-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 4rem 0;
        article {
            form {
                h3 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    color: #000000;
                    font-family: "Poppins-Bold";
                    font-size: 30px;
                    margin-top: 25px;
                }
                label {
                    color: #A4A6A9;
                    font-family: "Poppins-Medium";
                }
                input, textarea {
                    background: #F6F8FA;
                    border-radius: 40px;
                    border: none;
                    height: 45px;
                    font-family: "Poppins-Regular";
                    &:focus {
                        box-shadow: none;
                    }
                }
                textarea {
                    height: auto;
                    border-radius: 25px;
                }
                ::placeholder {
                    color: #C4C6CA;
                }
                .form-actions {
                    text-align: end;
                    margin-top: 2rem;
                    button {
                        @media(max-width: 600px) {
                            min-width: 140px;
                        }
                        margin: 5px;
                        transition: all .3s;
                        font-family: "Poppins-SemiBold";
                        font-size: 15px;
                        outline: none;
                        border-radius: 25px;
                        padding: 5px;
                        min-width: 160px;
                        min-height: 40px;
                        outline: none;
                        &:first-child {
                            background-color: #E4E5E6;
                            border: 2px solid #E4E5E6;
                            color: #000000;
                            &:hover {
                                background-color: #CED0D1;
                                border-color: #CED0D1;
                            }
                        }
                        &:last-child {
                            background-color: #37B14A;
                            border: 2px solid #37B14A;
                            color: #fff;
                            &:hover {
                                background-color: #258734;
                                border-color: #258734;
                            }
                        }
                    }
                }
            }
        }
    }
}