.profile-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 4rem 0;
        article {
            .profile-sec {
                .profile-title {
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-end;
                    border-bottom: 10px solid #EFEFF0;
                    padding-bottom: 15px;
                    .title {
                        h3 {
                            @media(max-width: 600px) {
                                font-size: 22px;
                            }
                            color: #000000;
                            font-family: "Poppins-Bold";
                            font-size: 35px;
                        }
                        h5 {
                            @media(max-width: 600px) {
                                font-size: 17px;
                            }
                            color: #909295;
                            font-size: 25px;
                            font-family: "Poppins-Light";
                        }
                    }
                    .edit-btn {
                        button {
                            all: unset;
                            cursor: pointer;
                            img {
                                width: 30px;
                                @media(max-width: 600px) {
                                    width: 20px;
                                }
                            }
                        }
                    }
                }
            }
            .profile-data {
                margin: 2rem 0;
                .table-responsive {
                    table {
                        tbody {
                            tr {
                                th, td {
                                    @media(max-width: 600px) {
                                        font-size: 15px;
                                    }
                                    white-space: nowrap;
                                    font-size: 22px;
                                }
                                th {
                                    width: 0;
                                    color: #000000;
                                    font-family: "Poppins-Medium";
                                }
                                td {
                                    color: #909295;
                                    font-family: "Poppins-Light";
                                }
                            }
                        }
                    }
                }
            }
            .profile-actions {
                text-align: center;
                margin-top: 4rem;
                button {
                    @media(max-width: 600px) {
                        font-size: 18px;
                        min-width: 200px;
                        min-height: 45px;
                    }
                    margin: 5px;
                    text-transform: capitalize;
                    background: $mainColor;
                    color: black;
                    font-size: 25px;
                    padding: 8px;
                    min-width: 340px;
                    border-radius: 70px;
                    min-height: 62px;
                    outline: none;
                    border: none;
                    font-family: "Poppins-Bold";
                    transition: all .3s;
                    &:hover {
                        background-color: #000000;
                        color: $mainColor;
                    }
                }
            }
        }
    }
}