.loading-page {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    img {
        @media(max-width: 600px) {
            width: 100%;
        }
        height: auto;
        width: 70%;
    }
}