.place-your-order-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 7rem 0;
        article {
            .order-form {
                label {
                    color: #000000;
                    font-family: "Poppins-Bold";
                    font-size: 18px;
                }
                input {
                    font-size: 18px;
                    background: #F6F8FA;
                    border-radius: 40px;
                    border: none;
                    height: 45px;
                    font-family: "Poppins-Regular";
                    &:focus {
                        box-shadow: none;
                    }
                }
                ::placeholder {
                    color: #C4C6CA;
                }
            }
            .info {
                margin: 1rem 0;
                p {
                    color: #6D6E70;
                    font-family: "Poppins-Light";
                    text-align: center;
                    font-size: 18px;
                    max-width: 850px;
                    margin: auto;
                }
            }
            .direct-order {
                margin-top: 25px;
                button {
                    @media(max-width: 600px) {
                        font-size: 18px;
                        min-height: 45px;
                    }
                    margin-bottom: 20px;
                    text-transform: capitalize;
                    background: #FFDD00;
                    color: #000000;
                    font-size: 25px;
                    padding: 8px;
                    width: 100%;
                    border-radius: 70px;
                    min-height: 62px;
                    outline: none;
                    border: none;
                    font-family: "Poppins-Bold";
                    transition: all .3s;
                    &:hover {
                        background-color: #000000;
                        color: #FFDD00;
                    }
                }
            }
            .customize-order {
                margin-top: 25px;
                p {
                    @media(max-width: 600px) {
                        font-size: 18px;
                    }
                    margin-bottom: 30px;
                    text-align: center;
                    font-size: 40px;
                    color: #6D6E70;
                    font-weight: 300;
                    max-width: 850px;
                    margin: 30px auto;
                }
                button {
                    @media(max-width: 600px) {
                        font-size: 18px;
                        min-height: 45px;
                    }
                    margin-bottom: 20px;
                    text-transform: capitalize;
                    background: #E4E5E6;
                    color: #6D6E70;
                    font-size: 25px;
                    padding: 8px;
                    width: 100%;
                    border-radius: 70px;
                    min-height: 62px;
                    outline: none;
                    border: none;
                    font-family: "Poppins-Bold";
                    transition: all .3s;
                    &:hover {
                        background-color: #CED0D1;
                    }
                }
            }
        }
    }
}