.contact-page{
    background-color: #000000;
    .page-title {
        header {
            @extend %header;
            h1 {
                margin-bottom: 160px;
            }
        }
    }
    .page-content {
        padding: 4rem 0;
        background-color: #fff;
        article {
            .contact-content {
                display: flex;
                @media (max-width: 992px){
                    flex-direction: column;
                }
                // box-shadow: 0px 0px 29px #C2C2C2;
                border-radius: 43px;
                overflow: hidden;
                margin-top: -145px;
                background-color: #fff;
                //** Start Contact Form **//
                .form-content {
                    @media (min-width: 992px){
                        width: 55%;
                    }
                    @media (max-width: 992px){
                        order: 2;
                        width: 100%;
                    }
                    padding: 6%;
                    h5 {
                        @media(max-width: 600px) {
                            font-size: 18px;
                        }
                        font-family: "ProximaNovaExtrabold";
                        font-size: 30px;
                    }
                    form {
                        input {
                            background-color: #F6F8FA;
                            border: none;
                            outline: none;
                            font-family: "ProximaNovaRegular";
                            border-radius: 32px;
                            padding: 10px 25px;
                        }
                        .form-control:focus{
                            box-shadow: none;
                        }
                        textarea {
                            background-color: #F6F8FA;
                            border: none;
                            outline: none;
                            font-family: "ProximaNovaRegular";
                            border-radius: 32px;
                            padding: 10px 25px;
                        }
                        ::placeholder {
                            color: #C4C6CA;
                        }
                        button {
                            color: #000000;
                            background-color: #FCDD14;
                            // border: 4px solid #FCDD14;
                            margin: 20px 0;
                            outline: none;
                            padding: 5px 12px;
                            min-width: 170px;
                            font-size: 18px;
                            border-radius: 28px;
                            font-family: "Poppins-Bold";
                            transition: all .3s;
                            border: none;
                            &:hover {
                                background-color: #000000;
                                color: #FCDD14;
                            }
                        }
                    }
                }
                //** End Contact Form **//

                //** Start Contact Info **//
                .contact-info {
                    @media (min-width: 992px){
                        clip-path: polygon(4% 0px, 100% 0%, 100% 100%, 4% 100%, 4% 46%, 0px 44%, 4% 42%);
                        width: 45%;
                        padding: 0;
                    }
                    @media (max-width: 992px){
                        order: 1;
                        width: 100%;
                        clip-path: polygon(0% 0%, 100% 0%, 100% 95%, 54% 95%, 49% 100%, 45% 95%, 0 95%);
                    }
                    background-color: $mainColor;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    padding: 40px 0;;
                    .content {
                        .logo {
                            img {
                                width: 125px;
                            }
                        }
                        .address {
                            ul {
                                margin: 0;
                                li {
                                    color: #000000;
                                    font-family: "Poppins-SemiBold";
                                    font-size: 13px;
                                    a {
                                        text-decoration: none;
                                        color: #000000;
                                        font-family: "Poppins-SemiBold";
                                        transition: .3s;
                                        &:hover {
                                            color: #fff;
                                        }
                                    }
                                }
                            }
                        }
                        .social-icons {
                            ul {
                                margin: 0;
                                li {
                                    display: inline-block;
                                    margin: 5px;
                                    a {
                                        color: #000000;
                                        text-decoration: none;
                                        transition: all .3s;
                                        &:hover {
                                            color: #fff;
                                        }
                                    }
                                }
                            }
                            p {
                                margin-bottom: 0;
                                a {
                                    color: #000000;
                                    font-family: "Poppins-SemiBold";
                                    transition: .3s;
                                    text-decoration: none;
                                    &:hover {
                                        color: #fff;
                                    }
                                }
                            }
                        }
                    }
                }
                //** End Contact Info **//
            }

            //** Start Google Map **//
            .map-section {
                position: relative;
                top: 7px;
            }
            //** End Google Map **//
        }
    }
}
