.my-orders-page {
    background-color: #000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding: 4rem 0;
        article {
            .my-orders {
                .title {
                    h3 {
                        @media(max-width: 600px) {
                            font-size: 20px;
                        }
                        color: #000000;
                        font-family: "Poppins-Bold";
                        font-size: 25px;
                    }
                    p {
                        color: #404041;
                        font-family: "Poppins-Regular";
                    }
                    button {
                        margin-top: 10px;
                        transition: all .3s;
                        font-family: "Poppins-SemiBold";
                        font-size: 15px;
                        color: #000;
                        background-color: $mainColor;
                        border: none;
                        outline: none;
                        border-radius: 25px;
                        padding: 5px;
                        min-width: 160px;
                        transition: all .3s;
                        &:hover {
                            background-color: #000;
                            color: $mainColor;
                        }
                    }
                }
                .content {
                    margin-top: 3rem;
                    .table-header {
                        display: flex;
                        justify-content: space-between;
                        white-space: nowrap;
                        align-items: center;
                        header {
                            h4 {
                                @media(max-width: 600px) {
                                    font-size: 18px;
                                }
                                color: #000000;
                                font-family: "Poppins-Bold";
                                font-size: 20px;
                                margin: 0;
                                margin-right: 20px;
                            }
                        }
                        .table-actions {
                            input, select {
                                outline: none;
                                border: 2px solid #E4E5E6;
                                border-radius: 22px;
                                color: #808082;
                                font-family: "Poppins-Medium";
                                font-size: 14px;
                                padding: 5px;
                            }
                            ::placeholder {
                                color: #E4E5E6;
                            }
                            button {
                                all: unset;
                                cursor: pointer;
                                border: 2px solid #E4E5E6;
                                border-radius: 22px;
                                width: 25px;
                                height: 25px;
                                text-align: center;
                                display: inline-flex;
                                align-items: center;
                                justify-content: center;
                                margin: 5px 0;
                                transition: all .3s;
                                &:hover {
                                    box-shadow: 0 0 5px #ffdd00;
                                }
                                img {
                                    // width: 18px;
                                    height: 15px;
                                }
                            }
                        }
                    }
                    table {
                        white-space: nowrap;
                        margin-top: 1rem;
                        thead {
                            background-color: $mainColor;
                            font-family: "Poppins-Bold";
                            color: #000;
                            font-size: 14px;
                            th:first-child {
                                border-radius: 32px 0 0 32px;
                            }
                            th:last-child {
                                border-radius: 0 32px 32px 0;
                            }
                        }
                        tbody {
                            td {
                                color: #909295;
                                font-family: "Poppins-Medium";
                                font-size: 13px;
                                span {
                                    font-family: "Poppins-SemiBold";
                                    &.going {
                                        color: #37B14A;
                                    }
                                    &.start {
                                        color: #6D6E70;
                                    }
                                    &.end {
                                        color: #EA1C24;
                                    }
                                    &.paused {
                                        color: #F4901E;
                                    }
                                }
                                .action {
                                    text-align: center;
                                    button {
                                        padding: 1px;
                                        all: unset;
                                        cursor: pointer;
                                        border: 2px solid #E4E5E6;
                                        border-radius: 22px;
                                        width: 25px;
                                        height: 25px;
                                        text-align: center;
                                        display: inline-flex;
                                        align-items: center;
                                        justify-content: center;
                                        transition: all .3s;
                                        img {
                                            height: 15px;
                                        }
                                        &:hover {
                                            box-shadow: 0 0 5px #ffdd00;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .table-footer {
                        .pagination-sec {
                            margin-right: 1rem;
                            .page-link {
                                color: #909295;
                                font-family: "Poppins-Medium";
                                font-size: 13px;
                                border: 1px solid #E4E5E6;
                                border-radius: 4px;
                                margin-right: 5px;
                            }
                        }
                        .table-actions {
                            .content {
                                display: inline-flex;
                                white-space: nowrap;
                                align-items: center;
                                span {
                                    color: #909295;
                                    font-family: "Poppins-Medium";
                                    font-size: 13px;
                                }
                                input {
                                    outline: none;
                                    border: 2px solid #E4E5E6;
                                    border-radius: 4px;
                                    color: #808082;
                                    font-family: "Poppins-Medium";
                                    font-size: 14px;
                                    padding: 5px;
                                    width: 45px;
                                    text-align: center;
                                }
                                button {
                                    transition: all .3s;
                                    font-family: "Poppins-SemiBold";
                                    font-size: 14px;
                                    color: #000;
                                    background-color: $mainColor;
                                    border: none;
                                    outline: none;
                                    border-radius: 4px;
                                    padding: 5px;
                                    min-width: 40px;
                                    min-height: 35px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}