.blog-article-page  {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        padding-top: 4rem;
        article {
            //** Start Topics Search **//
            .topic-search {
                header {
                    h3 {
                        @media(max-width: 600px){
                            font-size: 20px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 35px;
                        margin-bottom: 5px;
                    }
                }
                article {
                    p {
                        color: #404041;
                        font-size: 18px;
                        font-family: "Poppins-Regular";
                    }
                    form {
                        margin-top: 30px;
                        font-family: "Poppins-Medium";
                        input {
                            background: #E4E5E6;
                            border-radius: 32px;
                            padding-left: 18px;
                            padding-right: 18px;
                            border: none;
                            &:focus {
                                box-shadow: none;
                            }
                        }
                        button {
                            background-color: $mainColor;
                            border-radius: 32px;
                            min-width: 45px;
                            border: none;
                            outline: none;
                            transition: all .3s;
                            &:hover {
                                transform: scale(1.05) translateY(-5px);
                            }
                            img {
                                width: 20px;
                            }
                        }
                    }
                }
            }
            //** End Topics Search **//

            //** Start Branding Principles Section **//
            .branding-principles {
                margin-top: 1rem;
                header {
                    h1 {
                        @media(max-width: 600px){
                            font-size: 23px;
                        }
                        font-family: "Poppins-Bold";
                        font-size: 45px;
                        margin-bottom: 5px;
                    }
                    h5 {
                        color: #404041;
                        font-size: 18px;
                        font-family: "Poppins-Regular";
                    }
                }
                article {
                    time {
                        color: #404041;
                        font-family: "Poppins-SemiBold";
                    }
                    ul {
                        display: flex;
                        flex-wrap: wrap;
                        margin-top: 15px;
                        li {
                            @media(max-width: 600px){
                                font-size: 10px;
                            }
                            color: #909295;
                            border: 2px solid #CED0D1;
                            border-radius: 5px;
                            font-family: "Poppins-Regular";
                            margin-right: 5px;
                            margin-bottom: 5px;
                            padding: 5px 10px;
                            font-size: 14px;
                        }
                    }
                }
            }
            //** End Branding Principles Section **//

        }

        //** Start Separator Bg **//
        .seprator-bg {
            overflow: hidden;
            margin: 1rem 0;
        }
        //** End Separator Bg **//

        //** Start Text Info **//
        .info-title {
            margin: 3rem 0;
            p {
                @media(max-width: 600px){
                    font-size: 14px;
                }
                color: #6D6E70;
                font-size: 18px;
                font-family: "Poppins-Regular";
            }
        }
        //** Start Text Info **//

        //** Start Topics Card **//
        .topics-cards {
            background-color: #F4F4F4;
            padding: 4rem 0;
            a {
                text-decoration: none;
            }
            header {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-bottom: 2rem;
                h3 {
                    @media(max-width: 600px){
                        font-size: 20px;
                    }
                    font-family: "Poppins-Bold";
                    font-size: 30px;
                    margin-bottom: 5px;
                }
                a {
                    font-family: "Poppins-SemiBold";
                    font-size: 16px;
                    margin-bottom: 5px;
                    color: #000000;
                    text-decoration: none;
                    transition: all .3s;
                    &:hover {
                        transform: translateX(5px);
                    }
                }
            }
            .topic-card {
                background-color: #000000;
                border-radius: 11px;
                overflow: hidden;
                &:hover {
                    .card-img {
                        img {
                            transform: scale(1.05);
                        }
                    }
                }
                .card-img {
                    overflow: hidden;
                    img {
                        transition: all .4s ease-in-out;
                    }
                }
                .card-body {
                    color: #fff;
                    padding: 10px 20px;
                    time {
                        font-family: "Poppins-Bold";
                        font-size: 14px;
                    }
                    h4 {
                        font-family: "Poppins-Bold";
                        font-size: 18px;
                        margin: 7px 0;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    p {
                        font-size: 15px;
                        font-family: "Poppins-Regular";
                        margin: 0;
                    }
                }
            }
        }
        //** End Topics Card **//
    }
}