.modal {
    z-index: 999999999;
}
.models-section {
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    .navbar-section {
        width: 100%;
    }
}
//** Start Subscribe Modal **//
.subscripe-modal {
    .modal-content {
        border: none;
        // box-shadow: 0px 0px 90px #AAA763;
        border-radius: 25px;
        overflow: hidden;
        .modal-header{
            border: none;
            padding: 0;
            button.close {
                position: absolute;
                right: 20px;
                top: 15px;
                color: #000000;
                opacity: 1;
                z-index: 99;
                outline: none;
                background: transparent;
                font-size: 24px;
                span {
                    text-shadow: none;
                }
            }
        }
        .modal-body {
            padding: 0;
            .img-title {
                background: $mainColor;
                padding: 40px 0;
                clip-path: polygon(0% 0%, 100% 0%, 100% 92%, 52% 92%, 50% 100%, 48% 92%, 0 92%);
                img {
                    width: 70px;
                }
            }
            .body-content {
                padding: 20px 45px;
                h4 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    font-weight: bold;
                    margin-bottom: 12px;
                    font-family: "ProximaNovaExtrabold";
                    text-transform: uppercase;
                    color: #000000;
                    font-size: 30px;
                }
                p {
                    color: #000000;
                    font-family: "ProximaNovaRegular";
                }
                form {
                    input {
                        background: #F6F8FA;
                        font-family: "ProximaNovaRegular";
                        border: none;
                        outline: none;
                        text-align: center;
                        border-radius: 32px;
                        &:focus {
                            box-shadow: none;
                        }
                    }
                    button {
                        min-height: 44px;
                        min-width: 200px;
                        margin-top: 10px;
                        font-size: 18px;
                        border: 3px solid #FFDD00;
                        border-radius: 28px;
                        font-family: "Poppins-Bold";
                        outline: none;
                        background-color: #FFDD00;
                        transition: all .3s;
                        color: #000000;
                        &:hover {
                            color: #FFDD00;
                            background-color: #000000;
                            border-color: #000000;
                        }
                    }
                }
            }
        }
    }
}
//** End Subscribe Modal **//

