//** Start Login Modal **//
.login-modal {
    ::-webkit-scrollbar {
        width: 0px;
    }
    ::-webkit-scrollbar-track {
        background-color: rgb(255, 255, 255);
    }
    ::-webkit-scrollbar-thumb {
        background-color: $mainColor;
    }
    .modal-content {
        border: none;
        // box-shadow: 0px 0px 90px #AAA763;
        border-radius: 25px;
        overflow: hidden;
        .modal-header{
            border: none;
            padding: 0;
            button.close {
                position: absolute;
                right: 20px;
                top: 15px;
                color: #000000;
                opacity: 1;
                z-index: 99;
                outline: none;
                background: transparent;
                font-size: 24px;
                span {
                    text-shadow: none;
                }
            }
        }
        .modal-body {
            padding: 0;
            .modal-title {
                background: $mainColor;
                padding: 50px 0;
                clip-path: polygon(0% 0%, 100% 0%, 100% 92%, 52% 92%, 50% 100%, 48% 92%, 0 92%);
                h4 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    font-weight: bold;
                    margin-bottom: 12px;
                    font-family: "ProximaNovaExtrabold";
                    text-transform: uppercase;
                    color: #000000;
                    font-size: 30px;
                }
            }
            .body-content {
                padding: 20px 45px;
                text-align: start;
                h4 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    margin-bottom: 12px;
                    font-family: "Poppins-Bold";
                    color: #000000;
                    font-size: 30px;
                }
                p {
                    color: #404041;
                    font-family: "Poppins-Regular";
                    font-family: "Poppins-Medium";
                }
                form {
                    label {
                        color: #A4A6A9;
                        font-family: "Poppins-Medium";
                        font-size: 15px;
                    }
                    ::placeholder {
                        color: #C4C6CA;
                    }
                    input {
                        background-color: #F6F8FA;
                        font-family: "Poppins-Regular";
                        border-radius: 33px;
                        border: none;
                        outline: none;
                        &:focus {
                            box-shadow: none;
                        }
                    }
                    .custom-control {
                        margin-bottom: 10px;
                        label {
                            @media(max-width: 600px) {
                                font-size: 14px;
                            }
                            color: #000000;
                            font-size: 18px;
                            font-family: "Poppins-Bold";
                        }
                    }
                    .forget-password {
                        margin-bottom: 10px;
                        a {
                            @media(max-width: 600px) {
                                font-size: 14px;
                            }
                            color: #000000;
                            font-size: 18px;
                            font-family: "Poppins-Bold";
                            text-decoration: none;
                            cursor: pointer;
                        }
                    }
                    button.submit-btn {
                        @media(max-width: 600px) {
                            font-size: 16px;
                        }
                        width: 100%;
                        margin-top: 10px;
                        font-size: 18px;
                        border: 4px solid #FFDD00;
                        border-radius: 28px;
                        font-family: "Poppins-Bold";
                        outline: none;
                        color: #000000;
                        background-color: $mainColor;
                        padding: 8px 5px;
                        transition: all .3s;
                        &:hover {
                            color: #FFDD00;
                            background-color: #000000;
                            border-color: #000000;
                        }
                    }
                    h5 {
                        color: #000000;
                        font-family: "Poppins-Bold";
                        font-size: 18px;
                        text-align: center;
                        margin: 15px 0;
                    }
                    button.scial-btn {
                        @media(max-width: 600px) {
                            font-size: 16px;
                        }
                        text-align: center;
                        position: relative;
                        margin-bottom: 10px;
                        width: 100%;
                        font-size: 18px;
                        border: 3px solid #FFDD00;
                        border-radius: 28px;
                        font-family: "Poppins-Bold";
                        outline: none;
                        color: #000000;
                        background-color: transparent;
                        padding: 8px 5px;
                        transition: all .3s;
                        &:hover {
                            background-color: #FFDD00;
                            border-color: #FFDD00;
                        }
                        i {
                            position: absolute;
                            position: absolute;
                            top: 33%;
                            left: 15px;
                            transition: all .3s;
                        }
                    }
                }
                .sign-up {
                    p {
                        text-align: center;
                        color: #404041;
                        font-family: "Poppins-Regular";
                        margin-bottom: 0;
                        a {
                            font-family: "Poppins-Bold";
                            color: #25A7DE;
                            cursor: pointer;
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }
}
//** End Login Modal **//

