        .modal-body {
            padding: 0;
            .modal-title {
                background: $mainColor;
                padding: 50px 0;
                clip-path: polygon(0% 0%, 100% 0%, 100% 92%, 52% 92%, 50% 100%, 48% 92%, 0 92%);
                h4 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    font-weight: bold;
                    margin-bottom: 12px;
                    font-family: "ProximaNovaExtrabold";
                    text-transform: uppercase;
                    color: #000000;
                    font-size: 30px;
                }
            }
            .body-content {
                padding: 20px 45px;
                text-align: start;
                h4 {
                    @media(max-width: 600px) {
                        font-size: 20px;
                    }
                    margin-bottom: 12px;
                    font-family: "Poppins-Bold";
                    color: #000000;
                    font-size: 30px;
                }
                p {
                    color: #404041;
                    font-family: "Poppins-Regular";
                    font-family: "Poppins-Medium";
                }
                form {
                    h3 {
                        @media(max-width: 600px) {
                            font-size: 20px;
                        }
                        color: #000000;
                        font-family: "Poppins-Bold";
                        font-size: 30px;
                        margin-top: 25px;
                    }
                    .custom-control-inline {
                        margin-right: 10%;
                    }
                    .custom-radio {
                        position: relative;
                        padding-left: 0rem !important;
                        .custom-control-input:focus~.custom-control-label::before {
                            box-shadow: 0 0 0 0.2rem #ffdd0066;
                        }
                        input[type="radio"] {
                            opacity: 0;
                        }
                        label {
                            padding-left: 2rem;
                            cursor: pointer;
                            margin-bottom: 7px !important;
                            &:before {
                                width: 18px;
                                height: 18px;
                                position: absolute;
                                top: 1px;
                                left: 0;
                                content: '';
                                display: inline-block;
                                -moz-border-radius: 50px;
                                -webkit-border-radius: 50px;
                                border-radius: 50px;
                                border: 1px solid #A4A6A9;
                                background: #fff;
                            }
                        }
                        input[type="radio"] {
                            margin:0px;
                            &:disabled + label {
                                color: #999;
                                &:before {
                                background-color: #A4A6A9;
                                }
                            }
                            &:checked + label::after {
                                content: '';
                                position: absolute;
                                top: 5px;
                                left: 4px;
                                display: inline-block;
                                font-size: 11px;
                                width: 10px;
                                height: 10px;
                                background-color: #FFDD00;
                                -moz-border-radius: 50px;
                                -webkit-border-radius: 50px;
                                border-radius: 50px;
                            }
                        }
                    }
                    label {
                        color: #A4A6A9;
                        font-family: "Poppins-Medium";
                        font-size: 15px;
                    }
                    input, select {
                        background: #F6F8FA;
                        border-radius: 40px;
                        border: none;
                        height: 45px;
                        font-family: "Poppins-Regular";
                        &:focus {
                            box-shadow: none;
                        }
                    }
                    select {
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        appearance: none;
                        /* Some browsers will not display the caret when using calc, so we put the fallback first */ 
                        background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) #F6F8FA no-repeat calc(100% - 15px) !important;
                    }
                    ::placeholder {
                        color: #C4C6CA;
                    }
                    .terms-conditions {
                        label {
                            color: #000000;
                            font-family: "Poppins-Regular";
                            font-size: 15px;
                            a {
                                font-family: "Poppins-Bold";
                                text-decoration: underline;
                                &:first-child {
                                    text-decoration: none;
                                }
                                color: #25A7DE;
                                strong {
                                    color: #000000;
                                }
                            }
                        }
                    }
                }
            }
            .body-footer {
                padding: 20px;
                button.submit-btn {
                    @media(max-width: 600px) {
                        font-size: 16px;
                    }
                    width: 100%;
                    margin-top: 10px;
                    font-size: 18px;
                    border: 4px solid #FFDD00;
                    border-radius: 28px;
                    font-family: "Poppins-Bold";
                    outline: none;
                    color: #000000;
                    background-color: $mainColor;
                    padding: 8px 5px;
                    transition: all .3s;
                    &:hover {
                        background-color: #000000;
                        border-color: #000000;
                        color: #FFDD00;
                    }
                }
                h5 {
                    color: #000000;
                    font-family: "Poppins-Bold";
                    font-size: 18px;
                    text-align: center;
                    margin: 15px 0;
                }
                button.scial-btn {
                    @media(max-width: 600px) {
                        font-size: 16px;
                    }
                    text-align: center;
                    position: relative;
                    margin-bottom: 10px;
                    width: 100%;
                    font-size: 18px;
                    border: 4px solid #FFDD00;
                    border-radius: 28px;
                    font-family: "Poppins-Bold";
                    outline: none;
                    color: #000000;
                    background-color: transparent;
                    padding: 8px 5px;
                    transition: all .3s;
                    &:hover {
                        background-color: #FFDD00;
                    }
                    i {
                        position: absolute;
                        position: absolute;
                        top: 33%;
                        left: 15px;
                        transition: all .3s;
                    }
                }
                .sign-up {
                    p {
                        text-align: center;
                        color: #404041;
                        font-family: "Poppins-Regular";
                        margin-bottom: 0;
                        a {
                            font-family: "Poppins-Bold";
                            color: #25A7DE;
                            cursor: pointer;
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }
}
//** End Sign Up Modal **//

