                    /* The container */
                    .container-checkbox {
                        display: block;
                        position: relative;
                        padding-left: 35px;
                        margin-bottom: 12px;
                        cursor: pointer;
                        font-size: 16px;
                        -webkit-user-select: none;
                        -moz-user-select: none;
