.home-page {
    //** Start Main Section **//
    background-color: #000000 !important;
    .main-sec {
        background-color: #fff;
        .carousel.slide {
            .carousel-indicators {
                align-items: center;
                li {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: #fff;
                    opacity: 1;
                    transition: all .3s;
                    &.active {
                        width: 50px;
                        height: 5px;
                        border-radius: 2px;
                        border: aliceblue;
                        background-color: $mainColor;
                    }
                }
            } 
            .carousel-inner {
                .carousel-item {
                    // background: url('../assets/home/home.jpg');
                    background-repeat: no-repeat !important;
                    background-size: cover !important;
                    background-position: inherit !important;
                    .main {
                        min-height: calc(100vh - 72.06px);
                    }
                    .carousel-caption {
                        position: static;
                        h1 {
                            @media(max-width: 600px) {
                                font-size: 30px;
                            }
                            text-transform: capitalize;
                            font-family: "Poppins-Bold";
                            font-size: 75px;
                        }
                        p {
                            @media(max-width: 600px) {
                                font-size: 18px;
                            }
                            font-size: 25px;
                            font-family: "Poppins-Light";
                        }
                        button {
                            @media(max-width: 600px) {
                                font-size: 18px;
                                min-width: 200px;
                                min-height: 45px;
                                margin-top: 10px;
                            }
                            margin: 40px 0;
                            text-transform: capitalize;
                            background: $mainColor;
                            color: black;
                            font-size: 35px;
                            padding: 8px;
                            min-width: 400px;
                            border-radius: 70px;
                            min-height: 72px;
                            outline: none;
                            border: none;
                            font-family: "Poppins-Bold";
                            transition: all .3s;
                            &:hover {
                                background-color: #000000;
                                color: $mainColor;
                            }
                        }
                    }
                }
            }
        }
    }
    //** End Main Section **//

    //** Start Testimonials Section **//
    .testimonials {
        background-color: #fff;
        padding: 3rem 0;
        header {
            h2 {
                @media(max-width: 600px) {
                    font-size: 25px;
                }
                color: #000000;
                font-family: "Poppins-Bold";
                font-size: 43px;
                text-align: center;
                margin-bottom: 30px;
            }
        }
        article {
            .testimonials-content {
                .swiper-wrapper {
                    .swiper-slide {
                        .testi-card {
                            box-shadow: 0px 0px 30px #CECECE;
                            border-radius: 50px;
                            padding: 2rem 30px;
                            text-align: center;
                            margin: 30px;
                            margin-top: 5rem;
                            width: 100%;
                            .card-img {
                                margin: -100px auto 15px;
                                width: 120px;
                                border-radius: 50%;
                                border: 4px solid $mainColor;
                                padding: 10px;
                                height: 120px;
                                background: #fff;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                img {
                                    width: 75px;
                                    height: 75px;
                                }
                            }
                            .card-title {
                                h3 {
                                    @media(max-width: 600px) {
                                        font-size: 18px;
                                    }
                                    color: #000000;
                                    font-family: "Poppins-Bold";
                                    font-size: 33px;
                                }
                                h5 {
                                    @media(max-width: 600px) {
                                        font-size: 16px;
                                    }
                                    color: #6D6E70;
                                    font-family: "Poppins-Bold";
                                    font-size: 25px;
                                }
                            }
                            .card-info {
                                margin-top: 30px;
                                p {
                                    @media(max-width: 600px) {
                                        font-size: 14px;
                                    }
                                    color: #909295;
                                    font-size: 18px;
                                    font-family: "Poppins-Light";
                                }
                            }
                        }
                    }
                }
                .swiper-container {
                    .swiper-button-next, .swiper-button-prev {
                        background: none;
                        display: flex;
                        align-items: center;
                        i {
                            background: #fff;
                            width: 30px;
                            height: 27px;
                            border-radius: 50%;
                            text-align: center;
                            line-height: 28px;
                            box-shadow: 0px 0px 3px #e2dfdf;
                            font-size: 20px;
                            color: #808082;
                        }
                    }
                    .swiper-wrapper {
                        .swiper-slide {
                            .logos {
                                width: 50px;
                                // height: 50px;
                            }
                        }
                    }
                }
            }
        }
    }
    //** End Testimonials Section **//

    //** Start SEE OUR PORTOFOLIO Section **//
    .our-portofoliol  {
        background-color: $mainColor;
        padding-bottom: 50px;
        .carousel.slide {
            .carousel-indicators {
                justify-content: flex-start;
                padding-left: 5%;
                align-items: center;
                li {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background-color: #fff;
                    opacity: 1;
                    transition: all .3s;
                    &.active {
                        border-radius: 10px;
                        width: 50px;
                        height: 5px;
                        background-color: $mainColor;
                        border-radius: 2px;
                        border: aliceblue;
                    }
                }
            } 
            .carousel-inner {
                .carousel-item {
                    // background: url('../assets/home/portofolio.jpg');
                    background-repeat: no-repeat !important;
                    background-size: cover !important;
                    background-position: inherit !important;
                    .carousel-caption {
                        position: static;
                        text-align: start;
                        button {
                            @media(max-width: 600px) {
                                font-size: 18px;
                                min-width: 200px;
                                min-height: 45px;
                            }
                            margin-bottom: 40px;
                            text-transform: capitalize;
                            background: $mainColor;
                            color: black;
                            font-size: 25px;
                            padding: 8px;
                            min-width: 340px;
                            border-radius: 70px;
                            min-height: 62px;
                            outline: none;
                            border: none;
                            font-family: "Poppins-Bold";
                            transition: all .3s;
                            &:hover {
                                background-color: #000000;
                                color: $mainColor;
                            }
                        }
                    }
                }
            }
        }
    }
    //** End SEE OUR PORTOFOLIO Section **//

    //** Start Services Section **//
    .services-section {
        padding: 100px 0;
        background-color: #fff;
        .services-content  {
            .w-100.break {
                @media(max-width: 991px) {
                    display: none;
                }
            }
            a{
                text-decoration: none;
                color: #000000;
                .item-content {
                    margin: 40px 0;
                    &:hover {
                        img{
                            transform: translateY(-10px);
                        }
                        h4::before{
                            width: 25%;
                        }
                    }
                    img{
                        transition: .3s;
                        margin-bottom: 10px;
                        // width: 90px;
                        height: 90px;
                    }
                    h4{
                        @media(max-width: 600px) {
                            font-size: 14px;
                        }
                        font-size: 18px;
                        position: relative;
                        margin-top: 30px;
                        font-family: "Poppins-Bold";
                        color: #000000;
                        &::before{
                            content: "";
                            position: absolute;
                            left: 50%;
                            top: -16px;
                            bottom: 0;
                            height: 5px;
                            width: 20%;
                            background: $mainColor;
                            transition: .3s;
                            -webkit-transform: translateX(-50%);
                            transform: translateX(-50%);
                        }
                    }
                }
            }
        }
    }
    //** End Services Section **//
}