.about-page {
    background-color: #000000;
    .page-title  {
        header {
            @extend %header;
        }
    }
    .page-content {
        background-color: #fff;
        article {
            //** Start Who We Are & Vision & Mission & Part of US Section **//
            .who-we-are, .vision, .mission, .part-of-us {
                padding: 6rem 0;
                .content {
                    position: relative;
                    padding-left: 45px;
                    &::after {
                        content: '';
                        background: url("../assets/icons/text-icon.png");
                        background-size: contain;
                        background-repeat: no-repeat;
                        height: 33px;
                        width: 100px;
                        position: absolute;
                        left: 0px;
                        top: 7px;
                    }
                    header {
                        h3 {
                            color: #000000;
                            @media(max-width: 600px){
                                font-size: 22px;
                            }
                            font-family: "Poppins-Bold";
                            font-size: 35px;
                            margin-bottom: 25px;
                        }
                    }
                    article {
                        p {
                            @media(max-width: 600px){
                                font-size: 15px;
                            }
                            color: #000000;
                            font-family: "Poppins-Regular";
                            font-size: 18px;
                            margin: 0;
                        }
                        .action {
                            text-align: end;
                            button {
                                @media(max-width: 600px){
                                    font-size: 14px;
                                }
                                border-radius: 50px;
                                border: none;
                                min-width: 150px;
                                background: $mainColor;
                                color: black;
                                margin-bottom: 3rem;
                                font-family: "Poppins-Bold";
                                outline: none;
                                margin-top: 2rem;
                                font-size: 18px;
                                min-height: 37px;
                                transition: all .3s;
                                &:hover {
                                    background-color: #000000;
                                    color: $mainColor;
                                }
                            }
                        }
                    }
                }
            }
            //** End Who We Are & Vision & Mission & Part of US Section **//

            //** Start Think to Create Section  **//
            .think-to-create {
                background-color: #000000;
                .content {
                    text-align: center;
                    text-transform: uppercase;
                    img {
                        width: 70%;
                    }
                    h2 {
                        @media(max-width: 600px){
                            font-size: 45px;
                        }
                        font-family: "BebasNeueBold";
                        color: $mainColor;
                        font-size: 95px;
                        margin: 1rem;
                    }
                    button {
                        @media(max-width: 600px){
                            font-size: 18px;
                            min-height: 40px;
                        }
                        border-radius: 50px;
                        border: 2px solid $mainColor;
                        font-size: 25px;
                        min-width: 150px;
                        min-height: 50px;
                        background: $mainColor;
                        color: black;
                        margin-bottom: 3rem;
                        font-family: "Poppins-Bold";
                        outline: none;
                        transition: all .3s;
                        &:hover {
                            background-color: #000000;
                            color: $mainColor;
                        }
                    }
                }
            }
            //** End Think to Create Section **//

            //** Start Shoot for The Moon Section **//
            .shoot-moon {
                padding: 4rem 0;
                background-color: black;
                .section-title {
                    @media(max-width: 768px) {
                        text-align: center;
                    }
                    h1 {
                        @media(max-width: 600px){
                            font-size: 45px;
                            line-height: 40px;
                        }
                        font-weight: bold;
                        font-size: 80px;
                        font-family: BebasNeueBold;
                        line-height: 65px;
                        color: $mainColor;
                    }
                    button {
                        @media(max-width: 600px){
                            font-size: 18px;
                            min-height: 40px;
                        }
                        border-radius: 50px;
                        border: 2px solid $mainColor;
                        font-size: 25px;
                        min-width: 150px;
                        min-height: 50px;
                        background: $mainColor;
                        color: black;
                        margin: 1rem 0;
                        font-family: "Poppins-Bold";
                        width: auto;
                        outline: none;
                        transition: all .3s;
                        &:hover {
                            background-color: #000000;
                            color: $mainColor;
                        }
                    }
                }
            }
            //** End Shoot for The Moon Section **//

            //** Start Part of Us Section **//
            .ultimate-growth {
                background-color: #000000;
                padding-top: 3rem;
                .title {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    align-items: flex-start;
                    margin-bottom: 2rem;
                    h1 {
                        @media(max-width: 600px){
                            font-size: 45px;
                            line-height: 40px;
                        }
                        font-weight: bold;
                        font-size: 80px;
                        font-family: BebasNeueBold;
                        line-height: 65px;
                        color: $mainColor;
                    }
                    button {
                        @media(max-width: 600px){
                            font-size: 18px;
                            min-height: 40px;
                        }
                        border-radius: 50px;
                        border: 2px solid $mainColor;
                        font-size: 25px;
                        min-width: 150px;
                        min-height: 50px;
                        background: $mainColor;
                        color: black;
                        margin: 1rem 0;
                        font-family: "Poppins-Bold";
                        width: auto;
                        outline: none;
                        transition: all .3s;
                        &:hover {
                            background-color: #000000;
                            color: $mainColor;
                        }
                    }
                }
                .img {
                    text-align: center;
                    img {
                        max-width: 100%;
                    }
                }
            }
            //** End Part of Us Section **//
        }
    }
}